import { Form } from '@multiverses/verse-react/src/components/Form';
import PropTypes from 'prop-types';

import { useModel } from '../../../../../../verse-react/src/utility/hooks';
import { useApp } from '../../../../AppProvider';

export const Quantity = ({ model, type }) => {
  const finity = useModel('finity', {});
  const { quantity = {} } = useApp();
  const lot = quantity[finity[type].stockCode];

  return (
    <Form.Input
      defaultValue={lot}
      label="Quantity"
      model={model}
      placeholder="Quantity"
      step={String(lot)}
      type="number"
    />
  );
};

Quantity.propTypes = {
  model: PropTypes.string,
  type: PropTypes.string,
};

Quantity.defaultProps = {
  model: 'finity.quantity',
  type: 'base',
};
