import { Form } from '@multiverses/verse-react/src/components/Form';
import PropTypes from 'prop-types';

const options = ['call', 'put', 'others'];

export const Right = ({ model }) => {
  return (
    <Form.Select
      defaultValue="call"
      label="Right"
      model={model}
      options={options}
      placeholder="Right"
    />
  );
};

Right.propTypes = {
  model: PropTypes.string,
};

Right.defaultProps = {
  model: 'finity.right',
};
