import { useEffect } from 'react';

import { Datagrid } from '@multiverses/verse-react/src/components/Datagrid';

import { useModel } from '../../../../verse-react/src/utility/hooks';
import { useFinityApi } from '../../../hooks/useFinityApi';

export const PortfolioBook = () => {
  const { portfolioState, portfolioRequest } = useFinityApi('portfolio');
  const { portfolio = [] } = portfolioState;
  const finity = useModel('finity');

  useEffect(() => {
    if (finity.exchangeCode) {
      portfolioRequest({
        data: finity,
      });
    }
  }, [finity.exchangeCode]);

  const schema = {
    title: 'Mock Table',
    columns: [
      {
        dataKey: 'stock_code',
        title: 'Stock Code',
        width: 100,
        dataType: 'Number',
      },
      {
        dataKey: 'price',
        title: 'Price',
        width: 100,
        fixed: true,
        dataType: 'String',
      },
      {
        dataKey: 'pnl',
        title: 'Profit / Loss',
        width: 100,
        dataType: 'String',
      },
      {
        dataKey: 'exchange_code',
        title: 'Exchange Code',
        width: 100,
        dataType: 'String',
      },
      {
        dataKey: 'order_id',
        title: 'Order ID',
        width: 100,
        dataType: 'String',
      },
    ],
    enhancers: [
      {
        topRight: 'TOP_RIGHT',
      },
      {
        topLeft: 'TOP_LEFT',
      },
      {
        bottomRight: 'BOTTOM_RIGHT',
      },
      {
        bottomLeft: 'BOTTOM_LEFT',
      },
    ],
    titletip: true,
  };
  const data = [
    {
      id: 1,
      first_name:
        'Ingleberereferferfdecdcdcxexcecfddddddwtwecx3twercerw gddddtregerkongi9ewrjngi90ewrjvi90wejvgi90wejvgi90erjvi90ewfmjvi9wefmvi90-ef',
      last_name: 'Mapstone',
      email: 'imapstone0@freewebs.com',
      gender: 'Male',
    },
  ];

  console.log('$$', data, portfolio);
  return (
    <div className="flex-100">
      <Datagrid data={portfolio} height={185} schema={schema} />
    </div>
  );
};
