import React from 'react';

import { Accordion } from '@multiverses/verse-react/src/components/Accordion';
import { Badge } from '@multiverses/verse-react/src/components/Badge';
import { Datagrid } from '@multiverses/verse-react/src/components/Datagrid';
import { Skeleton } from '@multiverses/verse-react/src/components/Skeleton';
import { Text } from '@multiverses/verse-react/src/components/Text';
import PropTypes from 'prop-types';

const schema = {
  title: 'Mock Table',
  columns: [
    {
      dataKey: 'buyTime',
      title: 'Buy Date / Time',
      width: 160,
      dataType: 'String',
    },
    {
      dataKey: 'buyPrice',
      title: 'Buy Price',
      width: 80,
      dataType: 'String',
    },
    {
      dataKey: 'sellTime',
      title: 'Sell Date / Time',
      width: 160,
      dataType: 'String',
    },
    {
      dataKey: 'sellPrice',
      title: 'Sell Price',
      width: 80,
      dataType: 'String',
    },
    {
      dataKey: 'breakEvenPoint',
      title: 'BEP',
      width: 80,
      dataType: 'String',
    },
    {
      dataKey: 'acquiredPts',
      title: 'Acquired Pts',
      width: 80,
      dataType: 'String',
    },
    {
      dataKey: 'pnlBeforeExpense',
      title: 'EBE',
      width: 80,
      dataType: 'String',
    },
    {
      dataKey: 'totalExpense',
      title: 'Total Expense',
      width: 80,
      dataType: 'String',
    },
    {
      dataKey: 'pnl',
      title: 'PnL',
      width: 80,
      dataType: 'String',
    },
  ],
  titletip: true,
};

export const BackTestResults = ({ result }) => {
  return (
    <React.Fragment>
      <Accordion model="ema-strategy">
        <For each="data" index="resultIndex" of={result}>
          <Accordion.Wrapper accordionId={data.comboName} key={resultIndex}>
            <Accordion.Header>
              <div className="r-flex-5">
                <Badge badgeLabel={data.comboName}>{data.comboName}</Badge>
              </div>

              <div className="r-flex-2">
                <Skeleton keyName="EBE" title="PNL before expenses">
                  <Text format="currency_inr">{data.pnlBeforeExpense}</Text>
                </Skeleton>
              </div>
              <div className="r-flex-2">
                <Skeleton keyName="Charges">
                  <Text format="currency_inr">{data.charges}</Text>
                </Skeleton>
              </div>
              <div className="r-flex-2">
                <Skeleton keyName="Orders">{data.orderCount}</Skeleton>
              </div>
              <div className="r-flex-2">
                <Skeleton keyName="Winners">{data.winCount}</Skeleton>
              </div>
              <div className="r-flex-2">
                <Skeleton keyName="Losers">{data.loseCount}</Skeleton>
              </div>
              <div className="r-flex-2">
                <Skeleton keyName="Win %">{data.winPct}</Skeleton>
              </div>
              <div className="r-flex-2">
                <Skeleton keyName="PnL">
                  <Text format="currency_inr">{data.pnl}</Text>
                </Skeleton>
              </div>
            </Accordion.Header>
            <Accordion.Content>
              <div className="flex">
                <div className="r-flex-20">
                  <Datagrid
                    data={data.orders}
                    height={160}
                    schema={schema}
                    title="Orders"
                  />
                </div>
              </div>
            </Accordion.Content>
          </Accordion.Wrapper>
        </For>
      </Accordion>
    </React.Fragment>
  );
};

BackTestResults.propTypes = {
  result: PropTypes.arrayOf(
    PropTypes.shape({
      buyCount: PropTypes.number,
      charges: PropTypes.number,
      comboName: PropTypes.string,
      pnl: PropTypes.number,
      sellCount: PropTypes.number,
    })
  ),
};

BackTestResults.defaultProps = {
  result: {},
};
