import { Form } from '@multiverses/verse-react/src/components/Form';
import localDateTime from '@multiverses/verse-utility/date/localDateTime';
import PropTypes from 'prop-types';

export const ValidityDate = ({ model }) => {
  const date = localDateTime('9:15');
  return (
    <Form.Datepicker
      defaultValue={date}
      label="Validity Date"
      model={model}
      placeholder="Validity Date"
    />
  );
};

ValidityDate.propTypes = {
  model: PropTypes.string,
};

ValidityDate.defaultProps = {
  model: 'finity.validityDate',
};
