import { useEffect } from 'react';

import { Button } from '@multiverses/verse-react/src/components/Button';
import { Datagrid } from '@multiverses/verse-react/src/components/Datagrid';

import { useFinityApi } from '../../../hooks/useFinityApi';

export const OrderBook = () => {
  const { orderListState, orderListRequest } = useFinityApi('orderList');
  const { orderDeleteRequest } = useFinityApi('orderDelete');
  const { orderList = [] } = orderListState;

  useEffect(() => {
    orderListRequest({ data: {} });
  }, []);

  const handleOrderCancel = async rowData => {
    await orderDeleteRequest({ data: rowData });
    await orderListRequest({ data: {} });
  };

  const schema = {
    title: 'Mock Table',
    columns: [
      {
        dataKey: 'exchange_code',
        title: 'Exch',
        width: 70,
        dataType: 'String',
      },
      {
        dataKey: 'order_datetime',
        title: 'Order Date / Time',
        width: 160,
        dataType: 'String',
      },
      {
        dataKey: 'stock_code',
        title: 'Stock',
        width: 80,
        fixed: true,
        dataType: 'String',
      },
      {
        dataKey: 'order_type',
        title: 'Order Type',
        width: 80,
        fixed: true,
        dataType: 'String',
      },
      {
        dataKey: 'strike_price',
        title: 'Strike',
        width: 80,
        dataType: 'String',
      },
      {
        dataKey: 'expiry_date',
        title: 'Expiry Date',
        width: 100,
        dataType: 'String',
      },
      {
        dataKey: 'right',
        title: 'Right',
        width: 60,
        dataType: 'String',
      },
      {
        dataKey: 'stoploss',
        title: 'SL',
        width: 60,
        dataType: 'String',
      },
      {
        dataKey: 'price',
        title: 'Price',
        width: 90,
        dataType: 'String',
      },
      {
        dataKey: 'quantity',
        title: 'Qty',
        width: 80,
        dataType: 'String',
      },
      {
        dataKey: 'pending_quantity',
        title: 'Pending',
        width: 80,
        dataType: 'String',
      },
      {
        dataKey: 'status',
        title: 'Status',
        width: 80,
        dataType: 'String',
      },
      {
        dataKey: 'order_id',
        title: 'Actions',
        width: 160,
        dataType: 'String',
        cellRenderer: ({ rowData }) => {
          console.log('cellData', rowData);
          if (rowData.status === 'Cancelled') {
            return '---';
          }
          return (
            <div className="flex space-btw">
              <Button onClick={() => handleOrderCancel(rowData)}>Cancel</Button>
            </div>
          );
        },
      },
    ],
    titletip: true,
  };
  const data = [
    {
      id: 1,
      first_name:
        'Ingleberereferferfdecdcdcxexcecfddddddwtwecx3twercerw gddddtregerkongi9ewrjngi90ewrjvi90wejvgi90wejvgi90erjvi90ewfmjvi9wefmvi90-ef',
      last_name: 'Mapstone',
      email: 'imapstone0@freewebs.com',
      gender: 'Male',
    },
  ];

  console.log('$$', data, orderList);
  return (
    <div>
      <Datagrid data={orderList} height={360} schema={schema} />
    </div>
  );
};
