import { useEffect } from 'react';

import { Datagrid } from '@multiverses/verse-react/src/components/Datagrid';

import { useModel } from '../../../../verse-react/src/utility/hooks';
import { useFinityApi } from '../../../hooks/useFinityApi';

export const TradeBook = () => {
  const { tradesState, tradesRequest } = useFinityApi('trades');
  const { trades = [] } = tradesState;
  const finity = useModel('finity');

  useEffect(() => {
    if (finity.exchangeCode) {
      tradesRequest({
        data: finity,
      });
    }
  }, [finity.exchangeCode]);

  const schema = {
    title: 'Mock Table',
    columns: [
      {
        dataKey: 'order_exchange_code',
        title: 'Exch',
        width: 70,
        dataType: 'String',
      },
      {
        dataKey: 'order_trade_date',
        title: 'Trade Date',
        width: 100,
        dataType: 'String',
      },
      {
        dataKey: 'order_stock_code',
        title: 'Stock Code',
        width: 80,
        dataType: 'Number',
      },
      {
        dataKey: 'order_quantity',
        title: 'Quantity',
        width: 80,
        fixed: true,
        dataType: 'String',
      },
      {
        dataKey: 'order_average_executed_rate',
        title: 'Avg Exec Rate',
        width: 100,
        dataType: 'String',
      },
      {
        dataKey: 'order_trans_value',
        title: 'Trn Value',
        width: 100,
        dataType: 'String',
      },
      {
        dataKey: 'order_brokerage',
        title: 'Brokerage',
        width: 100,
        dataType: 'String',
      },
      {
        dataKey: 'order_total_taxes',
        title: 'Taxes',
        width: 100,
        dataType: 'String',
      },
      {
        dataKey: 'order_reference',
        title: 'Total',
        width: 100,
        dataType: 'String',
        dataGetter: ({ rowData }) => {
          return (
            Number(rowData.order_trans_value) +
            Number(rowData.order_brokerage) +
            Number(rowData.order_total_taxes)
          );
        },
      },
    ],
    titletip: true,
  };

  return (
    <div className="flex-100">
      <Datagrid data={trades} height={360} schema={schema} />
    </div>
  );
};
