import { Form } from '@multiverses/verse-react/src/components/Form';
import PropTypes from 'prop-types';

const options = [
  'futures',
  'options',
  'furtureplus',
  'futureplus_sltp',
  'optionplus',
  'cash',
  'eatm',
  'btst',
  'margin',
  'marginplus',
];

export const Product = ({ model, defaultValue }) => {
  return (
    <Form.Select
      defaultValue={defaultValue}
      label="Product"
      model={model}
      options={options}
      placeholder="Product"
    />
  );
};

Product.propTypes = {
  defaultValue: PropTypes.string,
  model: PropTypes.string,
};

Product.defaultProps = {
  defaultValue: 'cash',
  model: 'finity.product',
};
