import PropTypes from 'prop-types';

export const Badge = ({
  badgeLabel,
  badgeIndex,
  onClick,
  isDraggable,
  children,
  title,
  isActive,
  variant,
}) => {
  const onDragStart = ev => {
    if (isDraggable) {
      ev.dataTransfer.setData('id', `${badgeLabel}`);
    }
  };

  return (
    <span
      className={`badge m-r-4 ${isActive && 'active'} ${variant}`}
      draggable={isDraggable}
      onClick={() => onClick(badgeLabel, badgeIndex)}
      onDragStart={ev => onDragStart(ev, badgeLabel)}
      title={title}
    >
      <span>{children}</span>
    </span>
  );
};

Badge.propTypes = {
  badgeLabel: PropTypes.string.isRequired,
  badgeIndex: PropTypes.number,
  children: PropTypes.node,
  isActive: PropTypes.bool,
  isDraggable: PropTypes.bool,
  onClick: PropTypes.func,
  onDrop: PropTypes.func,
  title: PropTypes.string,
  variant: PropTypes.string,
};

Badge.defaultProps = {
  badgeIndex: 0,
  isActive: false,
  isDraggable: true,
  onClick: () => {},
  title: '',
  variant: '',
};
