import { Card } from '@multiverses/verse-react/src/components/Card';
import { MaterialIcon } from '@multiverses/verse-react/src/components/Icon';
import { Progress } from '@multiverses/verse-react/src/components/Progress';
import { Text } from '@multiverses/verse-react/src/components/Text';
import { localStorage } from '@multiverses/verse-react/src/utility/storage';

import { useModel } from '../../../../../verse-react/src/utility/hooks';

export const Rewards = () => {
  const pnl = useModel('finity.pnl');

  const handleProgress = wishIndex => {
    const wishArray = [...wishes].splice(0, wishIndex);
    let sum = 0;
    wishArray.forEach(a => {
      sum += a.points || 0;
    });
    // const wishValue = wishes[wishIndex].point;
    const newSum = pnl >= sum ? pnl - sum : 0;
    return newSum;
  };

  const wishes = localStorage.get('my-wishes');

  return (
    <div>
      <div className="flex space-btw">
        <For each="wish" index="wishIndex" of={wishes}>
          <Card className="flex-30 m-t-12" key={wish.code}>
            <div className="flex">
              <div className="flex-30">
                <div className="text-center">
                  <MaterialIcon name={wish.symbol} />
                </div>
                <Text align="center" format="currency_inr">
                  {wish.points}
                </Text>
              </div>
              <div className="flex-70">
                <div>{wish.name}</div>
                <Progress
                  handleProgress={() => handleProgress(wishIndex)}
                  max={wish.points}
                  model={wish.code}
                  value={pnl}
                />
              </div>
            </div>
          </Card>
        </For>
      </div>
    </div>
  );
};
