import { Form } from '@multiverses/verse-react/src/components/Form';
import { useForm } from '@multiverses/verse-react/src/utility/redux/slices/forms/formSlice';
import localDateTime from '@multiverses/verse-utility/date/localDateTime';
import PropTypes from 'prop-types';

export const FromDate = ({ model, ...props }) => {
  const form = useForm();
  const date = localDateTime('9:15');

  const onChange = () => {
    form.set({ model: `finity.endDate`, value: date });
  };

  return (
    <Form.Datepicker
      defaultValue={date}
      label="From Date"
      model={model}
      onChange={onChange}
      placeholder="From Date"
      {...props}
    />
  );
};

FromDate.propTypes = {
  model: PropTypes.string,
};

FromDate.defaultProps = {
  model: 'finity.fromDate',
};
