import { useEffect, useState } from 'react';

import { Navbar } from '@multiverses/verse-react/src/components/Navigation/Navbar';
import { Route, Routes } from '@multiverses/verse-react/src/components/Route';
import { SideBar } from '@multiverses/verse-react/src/components/SideBar';

import { BackTest } from './BackTest';
import { Book } from './Book';
import { Dashboard } from './Dashboard';
import { Funds } from './Funds';
import { IDBManager } from './IDBManager';
import { LiveTest } from './LiveTest';
import { OptionChain } from './OptionChain';
import { Rewards } from './Rewards';
import { RunStrategy } from './RunStrategy';
import { wsOHLCV } from '../../../breeze/WebSockets/OHLCVStream';
import { wsOrder } from '../../../breeze/WebSockets/OrderStream';
import { wsTICKS } from '../../../breeze/WebSockets/TickStream';
import { finityDB } from '../../../idb';
import { AppProvider } from '../../AppProvider';
import { getStock } from '../../functions/stock';
// import { Time } from '../../widgets/common/FormFields';
import { DateForm } from '../../widgets/common/Forms/DateForm';
import { Execute } from '../../widgets/Execute';

export const mainTools = [
  {
    name: 'Home',
    icon: 'home',
    to: '',
    end: true,
  },
  {
    name: 'Trade',
    icon: 'data_exploration',
    to: 'runstrategy',
  },
  {
    name: 'Paper Trade',
    icon: 'description',
    to: 'livetest',
  },
  {
    name: 'Back Test',
    icon: 'token',
    to: 'backtest',
  },
];

export const Home = () => {
  const [stockData, setStockData] = useState({});
  const [showSideNav, setShowSideNav] = useState(true);
  const breeze = {};
  const initStockData = async () => {
    const scripts = await getStock();
    setStockData(scripts);
    return scripts;
  };

  const onMenuClick = () => {
    setShowSideNav(!showSideNav);
  };

  useEffect(() => {
    async function initApp() {
      // eslint-disable-next-line no-unused-vars
      const scripts = await initStockData();
      // wsOHLCV.init(scripts.cdKeys);
      // wsOrder.init();
      // wsTICKS.init();
    }
    initApp();
  }, []);

  return (
    <AppProvider
      value={{ breeze, finityDB, wsOHLCV, wsTICKS, wsOrder, ...stockData }}
    >
      <div className="abs-right">
        <Navbar
          className="fdr-reverse"
          onMenuClick={onMenuClick}
          showMenu={showSideNav}
        >
          <Navbar.NavItemRight>
            <div className="flex space-btw m-b-4">
              {/* <Time label="" /> */}
              <DateForm />
              <Execute />
            </div>
          </Navbar.NavItemRight>
        </Navbar>
      </div>
      <div className="flex">
        <div className="flex-9">
          <div className="m-4 p-4">
            <Routes>
              <Route element={<Dashboard />} path="/dashboard" />
              <Route element={<BackTest />} path="/backtest" />
              <Route element={<LiveTest />} path="/livetest" />
              <Route element={<RunStrategy />} path="/runstrategy" />
              <Route element={<Book />} path="/book" />
              <Route element={<Funds />} path="/funds" />
              <Route element={<Rewards />} path="/rewards" />
              <Route element={<OptionChain />} path="/optionchain" />
              <Route element={<IDBManager />} path="/idbmanager" />
              <Route element={<Dashboard />} path="/" />
            </Routes>
          </div>
        </div>
        <SideBar contents={mainTools} show={showSideNav} />
      </div>
    </AppProvider>
  );
};
