import { Form } from '@multiverses/verse-react/src/components/Form';
import { localStorage } from '@multiverses/verse-react/src/utility/storage';
import PropTypes from 'prop-types';

export const Time = ({ model }) => {
  const onChange = value => {
    localStorage.set('PROCESS_TIME', value);
  };
  
  return (
    <div className="flex-20">
      <Form.Input
        defaultValue={920}
        model={model}
        onChange={onChange}
        placeholder="Time"
        step={'5'}
        type="Number"
      />
    </div>
  );
};

Time.propTypes = {
  model: PropTypes.string,
  type: PropTypes.string,
};

Time.defaultProps = {
  model: 'finity.stockCode',
  type: 'base',
};
