import {
  FromDate,
  Interval,
  OrderType,
  Quantity,
  Stoploss,
  ToDate,
} from '../../FormFields';

export const OrderForm = () => {
  return (
    <>
      <div className="flex m-x-8 space-btw">
        <div className="flex-30">
          <OrderType />
        </div>
        <div className="flex-30">
          <Quantity model="finity.base.quantity" />
        </div>
        <div className="flex-30">
          <Interval />
        </div>
      </div>
      <div className="flex m-x-8 space-btw">
        <div className="flex-30">
          <FromDate />
        </div>
        <div className="flex-30">
          <ToDate />
        </div>
        <div className="flex-30">
          <Stoploss />
        </div>
      </div>
    </>
  );
};
