import exponentialMovingAverage from '@multiverses/verse-utility/technicalIndicators/exponentialMovingAverage';

const getEmaCombos = (baseSet, e1, e2) => {
  // const emas = [1, 2, 3, 5, 7, 9, 11, 14, 15, 17, 20];
  const emas = [e1, e2];
  const emaValues = {};
  emas.forEach(ema => {
    const p1 = { datasets: baseSet, period: ema, valueKey: 'close' };
    emaValues[ema] = exponentialMovingAverage(p1);
  });
  return emaValues;
};

export const ema = [
  {
    name: 'EMA Up Down',
    description:
      'Runs EMA combos and gives the highest probability EMA for profit.',
    buyLookUp: (baseSet, e1 = 1, e2 = 3) => {
      const emaValues = getEmaCombos(baseSet, e1, e2);
      const emaCombo = { e1, e2 };
      const slicelength = emaCombo.e2 - emaCombo.e1;
      const ema1s = emaValues[emaCombo.e1].slice(
        slicelength,
        emaValues[emaCombo.e1].length
      );
      const ema2s = emaValues[emaCombo.e2];
      const emaLength = ema1s.length;
      const ema1L = ema1s[emaLength - 1].ema;
      const ema2L = ema2s[emaLength - 1].ema;

      /** Has No Buy Order, if fast ema beates slow ema, make a buy */
      console.log(`BUY`, ema1L, '---', ema2L);
      if (ema1L > ema2L) {
        // console.log(
        //   'EMA B',
        //   ema1s[emaLength - 1].datetime,
        //   '--',
        //   ema1L,
        //   '-----',
        //   ema2L
        // );
        return true;
      }
      return false;
    },
    sellLookUp: (baseSet, e1 = 1, e2 = 3) => {
      const emaValues = getEmaCombos(baseSet, e1, e2);
      const emaCombo = { e1, e2 };
      const slicelength = emaCombo.e2 - emaCombo.e1;
      const ema1s = emaValues[emaCombo.e1].slice(
        slicelength,
        emaValues[emaCombo.e1].length
      );
      const ema2s = emaValues[emaCombo.e2];
      const emaLength = ema1s.length;
      const ema1L = ema1s[emaLength - 1].ema;
      const ema2L = ema2s[emaLength - 1].ema;

      /** Has No Buy Order, if slow ema beates fast ema, make a sell */
      console.log(`SELL`, ema1L, '---', ema2L);
      if (ema2L > ema1L) {
        // console.log(
        //   'EMA S',
        //   ema1s[emaLength - 1].datetime,
        //   '--',
        //   ema1L,
        //   '-----',
        //   ema2L
        // );
        return true;
      }
      return false;
    },
  },
];
