// /* eslint-disable */
import { useEffect, useState } from 'react';

import { Button } from '@multiverses/verse-react/src/components/Button';
import { PlayPause } from '@multiverses/verse-react/src/components/PlayPause';
import { useModel } from '@multiverses/verse-react/src/utility/hooks';
import { localStorage } from '@multiverses/verse-react/src/utility/storage';

import { useFinityApi } from '../../../hooks/useFinityApi';
import { useApp } from '../../AppProvider';
import { LiveResults } from '../../component/LiveResults';
import { getToken } from '../../functions/stock';
import { strategiesList } from '../../resources/strategies/live';
import { StrategyList } from '../common/FormFields/StrategyList';
import { TrackForm } from '../common/Forms/TrackForm';

// const statusMessage = {
//   R: 'Requested, Request taken by ICICI direct system.',
//   Q: 'Queued, Request sent to Exchange.',
//   O: 'Request is open at Exchange.',
//   P: 'Request is partly filled at Exchange.',
//   E: 'Request is executed at Exchange.',
//   J: 'Request is rejected by exchange/ICICI direct system.',
//   X: 'Request expired by exchange/ICICI direct system',
//   C: 'Cancelled.',
//   undefined: 'Status message is unavailable.',
// };

const typeCodes = {
  'buy': 'Buy',
  'sell': 'Sell',
};

export const LiveTest = () => {
  const [logs, setLogs] = useState([]);
  const [results, setResults] = useState({});
  const { finityDB, wsOHLCV, wsOrder, wsTICKS, cdKeys } = useApp();
  const [bData, setBData] = useState({});
  const [tData, setTData] = useState({});
  const [status, setStatus] = useState(false);
  const finity = useModel('finity');
  const { orderRequest } = useFinityApi('order');

  const handleLogs = async () => {
    if (finityDB.logs) {
      const logs = await finityDB.logs.getAll();
      setLogs(logs.reverse());
    }
  };

  const handleOrders = (orders = []) => {
    // const results = brokerage('options', orders, finity.track.quantity);
    setResults(orders);
  };

  // eslint-disable-next-line no-unused-vars
  const addLog = async message => {
    await finityDB.logs.add({
      stockCode: finity.track.stockCode,
      interval: finity.interval,
      datetime: Date.now(),
      message,
    });
    handleLogs();
  };

  const addOrders = async orderData => {
    const orders = await finityDB.orders.edit({
      stockCode: orderData.stockCode,
      datetime: orderData.messageTime,
      date: orderData.messageDate,
      price: orderData.limitRate,
      type: typeCodes[orderData.orderFlow],
      orderReference: orderData.orderReference,
      quantity: orderData.orderTotalQuantity,
      orderStatus: orderData.orderStatus,
      expense: orderData.expense,
    });
    handleOrders(orders);
  };

  // eslint-disable-next-line no-unused-vars
  const executeOrder = async (action, tData) => {
    // eslint-disable-next-line no-constant-condition
    if (1 === 2) {
      try {
        const orderResponse = await orderRequest({
          data: finity,
          type: 'track',
          action,
          price: tData.close,
        });
        if (orderResponse.error?.message) {
          setStatus(false);
          wsOHLCV.disconnect();
          return false;
        } else {
          const orderData = orderResponse.payload.order_id;
          return orderData;
        }
      } catch (err) {
        setStatus(false);
        wsOHLCV.disconnect();
        alert('Order Failure', err);
        return false;
      }
    }
  };

  useEffect(() => {
    async function getAllLogs() {
      handleLogs();
    }
    getAllLogs();
  }, [finityDB.logs]);

  useEffect(() => {
    async function getAllOrders() {
      if (finityDB.orders) {
        const orders = await finityDB.orders.getAll();
        handleOrders(orders);
      }
    }
    getAllOrders();
  }, [finityDB.orders]);

  const onOrderFeed = async orderData => {
    addOrders(orderData);
    const orderCode = `${orderData.orderFlow}${orderData.orderStatus}`;
    localStorage.set('orderStatus', orderCode);
    if (orderCode === 'BC') {
      const orders = await finityDB.orders.delete(orderData.orderReference);
      handleOrders(orders);
    }
  };

  const onLiveRunStart = async () => {
    // Watch Breeze Socket
    const baseLabel = getToken(finity.base);
    const trackLabel = getToken(finity.track);
    const baseToken = cdKeys[finity.base.exchCode][baseLabel].token;
    const trackToken = cdKeys[finity.track.exchCode][trackLabel].token;
    await wsTICKS.precheck({
      strategy: strategiesList[finity.strategy],
      baseToken,
      trackToken,
      setBase: setBData,
      setTrack: setTData,
      onOrder: onOrderFeed,
      finity,
      onStop: () => {
        setStatus(false);
      },
    });
    console.log('---------------------');
    console.log('---------STARTING LIVE------------');
    // wsOHLCV.on();
    // wsOrder.on(onOrderFeed);
    wsTICKS.on(baseToken);
    setStatus(true);
  };

  const onLiveRunStop = () => {
    // Stop Breeze Socket
    wsTICKS.disconnect();
    wsOHLCV.disconnect();
    wsOrder.disconnect();
    setStatus(false);
  };

  const onDisconnect = () => {
    // Stop Breeze Socket
    onLiveRunStop();
    if (wsOHLCV.orderStatus === 'BE') {
      alert('Has pending buy');
    }
  };

  return (
    <div className="flex space-btw">
      <div className="r-flex-5">
        <div className="flex">
          <div className="r-flex-16">
            <StrategyList />
          </div>
          <div className="r-flex-4">
            <div className="flex flex-center">
              <div className="m-t-28">
                <PlayPause play={status} />
              </div>
            </div>
          </div>
        </div>
        <div className="flex space-btw m-t-12">
          <div className="r-flex-9">
            <Button className="btn error" onClick={onLiveRunStop}>
              Stop
            </Button>
          </div>
          <div className="r-flex-9">
            <Button
              className="btn success"
              // isLoading={historyState.loading}
              onClick={onLiveRunStart}
            >
              Start
            </Button>
          </div>
        </div>
        <TrackForm />
      </div>
      <div className="r-flex-15">
        <div className="p-l-8">
          <LiveResults
            bData={bData}
            logs={logs}
            onDisconnect={onDisconnect}
            results={results}
            status={status}
            tData={tData}
          />
        </div>
      </div>
    </div>
  );
};
