import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { useDispatch, useSelector } from 'react-redux';

import { httpVerse } from '../../../http/httpVerse';

export const apiCall = createAsyncThunk('api/apiCall', async args => {
  const result = await httpVerse({
    ...args,
  })
    .then(res => {
      return res.data;
    })
    .catch(err => {
      throw err.data;
    });
  return result;
});

export const apiSlice = createSlice({
  name: 'api',
  initialState: {},
  reducers: {
    resetMessage: (state, action) => {
      console.log(state, action);
      return {
        ...state,
        ...{
          [action.payload.model]: {
            [action.payload.model]: undefined,
            error: {},
            loading: false,
          },
        },
      };
    },
  },
  extraReducers: {
    [apiCall.fulfilled]: (state, action) => {
      return {
        ...state,
        ...{
          [action.meta.arg.model]: {
            [action.meta.arg.model]: action.payload,
            error: {},
            ...(action.meta.arg.options || {}),
            loading: false,
          },
        },
      };
    },
    [apiCall.pending]: (state, action) => {
      return {
        ...state,
        ...{
          [action.meta.arg.model]: {
            [action.meta.arg.model]: undefined,
            error: {},
            loading: true,
          },
        },
      };
    },
    [apiCall.rejected]: (state, action) => {
      return {
        ...state,
        ...{
          [action.meta.arg.model]: {
            [action.meta.arg.model]: undefined,
            error: action.error,
            loading: false,
          },
        },
      };
    },
  },
});

export const { resetMessage, setResponse } = apiSlice.actions;

export const useApi = (model, route = {}, options) => {
  const dispatch = useDispatch();
  return data => dispatch(apiCall({ ...route, ...data, model, options }));
};

export const useApiState = (
  model,
  defaultValue = { error: '', loading: false }
) => {
  return useSelector(state => state.api?.[model] || defaultValue);
};

export const useApiHelpers = model => {
  const dispatch = useDispatch();
  const titleName = model.charAt(0).toUpperCase() + model.slice(1);
  return {
    [`reset${titleName}Message`]: () => dispatch(resetMessage({ model })),
  };
};

export default apiSlice.reducer;
