import { Form } from '@multiverses/verse-react/src/components/Form';
import localDateTime from '@multiverses/verse-utility/date/localDateTime';
import PropTypes from 'prop-types';

export const ToDate = ({ model, ...props }) => {
  const date = localDateTime('15:20');
  return (
    <Form.Datepicker
      defaultValue={date}
      label="To Date"
      model={model}
      placeholder="To Date"
      {...props}
    />
  );
};

ToDate.propTypes = {
  model: PropTypes.string,
};

ToDate.defaultProps = {
  model: 'finity.toDate',
};
