import { Alert } from '@multiverses/verse-react/src/components/Alert';
import {
  Tab,
  TabContent,
  Tabs,
  TabTitle,
} from '@multiverses/verse-react/src/components/Tabs';

import { useFinityApi } from '../../../../hooks';
import { OrderBook } from '../../../widgets/OrderBook/OrderBook';
import { PortfolioBook } from '../../../widgets/PortfolioBook';
import { TradeBook } from '../../../widgets/TradeBook';

export const Book = () => {
  const { portfolioState, resetPortfolioMessage } = useFinityApi('portfolio');

  return (
    <div>
      <Alert
        autoDismiss
        className="m-x-8"
        errorMessage={portfolioState.error.message}
        message={portfolioState.portfolio?.message}
        onDismiss={resetPortfolioMessage}
      />
      <Tabs defaultTab={1} model="modelTab">
        <TabTitle>
          <Tab span={40}>Order Book</Tab>
          <Tab>Trade</Tab>
          <Tab>Portfolio</Tab>
        </TabTitle>
        <TabContent>
          <OrderBook />
        </TabContent>
        <TabContent>
          <TradeBook />
        </TabContent>
        <TabContent>
          <PortfolioBook />
        </TabContent>
      </Tabs>
    </div>
  );
};
