import { Form } from '@multiverses/verse-react/src/components/Form';
import PropTypes from 'prop-types';

const options = ['day', 'ioc', 'vtc'];

export const Validity = ({ model }) => {
  return (
    <Form.Select
      defaultValue="ioc"
      label="Validity"
      model={model}
      options={options}
      placeholder="Validity"
    />
  );
};

Validity.propTypes = {
  model: PropTypes.string,
};

Validity.defaultProps = {
  model: 'finity.validity',
};
