import { Form } from '@multiverses/verse-react/src/components/Form';
import PropTypes from 'prop-types';

import { useModel } from '../../../../../../verse-react/src/utility/hooks';
import { useApp } from '../../../../AppProvider';

export const StrikePrice = ({ model }) => {
  const finity = useModel('finity', {});
  const { options = {} } = useApp();

  return (
    <Form.Select
      defaultValue={16900}
      label="Strike Price"
      labelKey="strike"
      model={model}
      options={options[finity.track.stockCode]}
      placeholder="Strike Price"
      valueKey="strike"
    />
  );
};

StrikePrice.propTypes = {
  model: PropTypes.string,
};

StrikePrice.defaultProps = {
  model: 'finity.strikePrice',
};
