const fixedBrokerage = 20;

const roundTo = num => {
  return Number(num.toFixed(2));
};

export const brokerageByProduct = {
  cash: {
    brokerage: ({ price }) => {
      const calculatedBrokerage = 0.025 * price;
      return calculatedBrokerage <= fixedBrokerage
        ? calculatedBrokerage
        : fixedBrokerage;
    },
    stt: ({ price }) => {
      return price * (0.025 / 100);
    },
    exchTxnChgs: ({ price }) => {
      return price * (0.00345 / 100);
    },
    stampDuty: ({ price }) => {
      return price * (0.003 / 100);
    },
    sebiCharges: ({ price }) => {
      return (0.0001 / 100) * price;
    },
  },
  options: {
    brokerage: () => {
      return fixedBrokerage;
    },
    stt: ({ price }) => {
      return price * (0.05 / 100);
    },
    exchTxnChgs: ({ price }) => {
      return price * (0.053 / 100);
    },
    stampDuty: ({ price }) => {
      return price * (0.003 / 100);
    },
    sebiCharges: ({ price }) => {
      return (0.0001 / 100) * price;
    },
  },
  futures: {
    brokerage: () => {
      return 0;
    },
    stt: ({ price }) => {
      return price * (0.01 / 100);
    },
    exchTxnChgs: ({ price }) => {
      return price * (0.002 / 100);
    },
    stampDuty: ({ price }) => {
      return price * (0.002 / 100);
    },
    sebiCharges: ({ price }) => {
      return (0.0001 / 100) * price;
    },
  },
};

export const brokerage = (productType, transactions) => {
  const orders = [];
  let winCount = 0;
  let loseCount = 0;
  // console.log('Product', productType);
  for (let i = 0; i < transactions.length; i = i + 2) {
    const product = brokerageByProduct[productType];
    const buyTxn = transactions[i];
    // console.log('Buy Txn :: ', buyTxn);
    const quantity = buyTxn.quantity || 50;
    const sellTxn = transactions[i + 1] || { price: 0 };
    // console.log('Sell Txn :: ', sellTxn);
    const buyValue = buyTxn.price * quantity;
    const sellValue = sellTxn.price * quantity;
    const turnover = buyValue + sellValue;
    // console.log('Turnover :: ', turnover);
    const sebiCharges =
      product.sebiCharges({
        price: buyTxn.price + sellTxn.price,
      }) * quantity;
    // console.log('SEBI Chgs :: ', sebiCharges);

    const buyBrokerage = product.brokerage(buyTxn);
    // console.log('Buy Brokerage :: ', buyBrokerage);
    const buyExchTxnChgs = product.exchTxnChgs(buyTxn) * quantity;
    // console.log('Buy Exch Txn Chgs :: ', buyExchTxnChgs);
    const buyStampDuty = product.stampDuty(buyTxn) * quantity;
    // console.log('Buy Stamp Duty :: ', buyExchTxnChgs);

    const sellBrokerage = product.brokerage(sellTxn);
    // console.log('Sell Brokerage :: ', sellBrokerage);
    const sellExchTxnChgs = product.exchTxnChgs(sellTxn) * quantity;
    // console.log('Sell Exch Txn Chgs :: ', sellExchTxnChgs);
    const sellSTT = product.stt(sellTxn) * quantity;
    // console.log('Sell STT :: ', sellSTT);

    const gst =
      0.18 *
      (buyBrokerage +
        sellBrokerage +
        buyExchTxnChgs +
        sellExchTxnChgs +
        sebiCharges);
    // console.log('GST :: ', gst);

    const buyExpense = buyBrokerage + buyExchTxnChgs + buyStampDuty;
    // console.log('Buy Expense :: ', buyExpense);
    const sellExpense = sellBrokerage + sellExchTxnChgs + sellSTT;
    // console.log('Sell Expense :: ', sellExpense);
    const totalExpense = buyExpense + sellExpense + gst + sebiCharges;
    // console.log('Total Expense :: ', totalExpense);
    const breakEvenPoint = totalExpense / quantity;
    // console.log('BEP :: ', breakEvenPoint);
    const pnl = sellValue - buyValue - totalExpense;
    // console.log('PNL :: ', pnl);
    pnl >= 0 ? winCount++ : loseCount++;

    orders.push({
      pnl: roundTo(pnl),
      pnlBeforeExpense: roundTo(sellValue - buyValue),
      acquiredPts: roundTo(sellTxn.price - buyTxn.price),
      breakEvenPoint: roundTo(breakEvenPoint),
      totalExpense: roundTo(totalExpense),
      buyPrice: buyTxn.price,
      sellPrice: sellTxn.price,
      buyValue: roundTo(buyValue),
      sellValue: roundTo(sellValue),
      turnover: roundTo(turnover),
      buyExpense: roundTo(buyExpense),
      sellExpense: roundTo(sellExpense),
      buyTime: buyTxn.datetime,
      sellTime: sellTxn.datetime,
      buyExchTxnChgs: roundTo(buyExchTxnChgs),
      sellExchTxnChgs: roundTo(sellExchTxnChgs),
      exchTxnChgs: roundTo(buyExchTxnChgs + sellExchTxnChgs),
      sebiCharges: roundTo(sebiCharges),
      gst: roundTo(gst),
      stt: roundTo(sellSTT),
      stampDuty: roundTo(buyStampDuty),
      brokerage: roundTo(buyBrokerage + sellBrokerage),
    });
  }

  const orderCount = orders.length;

  return {
    orders,
    orderCount,
    winCount,
    loseCount,
    charges: orders.reduce(function (acc, obj) {
      return acc + obj.totalExpense;
    }, 0),
    pnl: orders.reduce(function (acc, obj) {
      return acc + obj.pnl;
    }, 0),
    pnlBeforeExpense: orders.reduce(function (acc, obj) {
      return acc + obj.pnlBeforeExpense;
    }, 0),
    winPct: roundTo((winCount / orderCount) * 100),
    losePct: roundTo((loseCount / orderCount) * 100),
  };
};

export const perOrderExpense = (productType, order) => {
  // console.log('Product', productType);
  const product = brokerageByProduct[productType];
  const txn = order || { price: 0 };
  // console.log('Buy Txn :: ', buyTxn);
  const quantity = order.orderTotalQuantity;
  const sebiCharges =
    product.sebiCharges({
      price: txn.price,
    }) * quantity;
  // console.log('SEBI Chgs :: ', sebiCharges);

  const brokerage = product.brokerage(txn);
  // console.log('Buy Brokerage :: ', buyBrokerage);
  const exchTxnChgs = product.exchTxnChgs(txn) * quantity;
  // console.log('Buy Exch Txn Chgs :: ', buyExchTxnChgs);
  const stampDuty = product.stampDuty(txn) * quantity;
  // console.log('Buy Stamp Duty :: ', buyExchTxnChgs);

  const gst = 0.18 * (brokerage + exchTxnChgs + sebiCharges);
  // console.log('GST :: ', gst);

  const expense = brokerage + exchTxnChgs + stampDuty;
  // console.log('Buy Expense :: ', buyExpense);
  // console.log('Sell Expense :: ', sellExpense);
  const totalExpense = expense + gst + sebiCharges;
  // console.log('Total Expense :: ', totalExpense);

  return roundTo(totalExpense);
};
