import { Form } from '@multiverses/verse-react/src/components/Form';
import PropTypes from 'prop-types';

import { exchangeCodes } from '../../../../functions/stock';

export const ExchangeCode = ({ model, defaultValue }) => {
  return (
    <Form.Select
      defaultValue={defaultValue}
      label="Exchange"
      model={model}
      options={exchangeCodes}
      placeholder="Exch Code"
    />
  );
};

ExchangeCode.propTypes = {
  defaultValue: PropTypes.string,
  model: PropTypes.string,
};

ExchangeCode.defaultProps = {
  defaultValue: 'NSE',
  model: 'finity.exchangeCode',
};
