import React, { useEffect, useMemo } from 'react';

import uniqBy from 'lodash/uniqBy';
import PropTypes from 'prop-types';
import VirtualizedSelect from 'react-virtualized-select';

import { useModel } from '../../../utility/hooks';
import { useForm } from '../../../utility/redux/slices/forms/formSlice';
import { Label } from '../Label';

import 'react-select/dist/react-select.css';
import 'react-virtualized/styles.css';
import 'react-virtualized-select/styles.css';

export const Select = ({
  id,
  model,
  label,
  options,
  onChange,
  placeholder,
  disabled,
  defaultValue,
  valueKey,
  className,
  labelKey,
}) => {
  const value = useModel(model);
  const { set } = useForm();

  const handleChange = option => {
    const value = valueKey ? option[valueKey] : option['value'];
    set({
      model,
      value,
      overWrite: true,
    });
    onChange(value);
  };
  const selectOptions = useMemo(() => {
    const isStringsArray = options.every(i => typeof i === 'string');
    if (isStringsArray) {
      const optionsMap = options.map(option => ({
        label: option,
        value: option,
      }));
      return uniqBy(optionsMap, 'value');
    }
    return uniqBy(options, valueKey);
  }, [options]);

  useEffect(() => {
    if (defaultValue) {
      set({ model, value: defaultValue, overWrite: true });
      onChange(defaultValue);
    }
  }, [defaultValue]);

  return (
    <React.Fragment>
      <If condition={label}>
        <Label>{label}</Label>
      </If>
      <VirtualizedSelect
        className={className}
        disabled={disabled}
        id={id}
        labelKey={labelKey}
        onChange={handleChange}
        options={selectOptions}
        placeholder={placeholder}
        value={value}
        valueKey={valueKey}
      />
    </React.Fragment>
  );
};

Select.propTypes = {
  className: PropTypes.string,
  defaultValue: PropTypes.any,
  disabled: PropTypes.bool,
  id: PropTypes.string,
  label: PropTypes.string,
  labelKey: PropTypes.string,
  labelName: PropTypes.string,
  model: PropTypes.string,
  onChange: PropTypes.func,
  options: PropTypes.any,
  placeholder: PropTypes.string,
  selectedValue: PropTypes.string,
  valueKey: PropTypes.string,
};

Select.defaultProps = {
  className: 'form-select primary-form',
  disabled: false,
  label: '',
  model: null,
  onChange: () => null,
  options: [],
};
