/* eslint-disable no-unused-vars */
import { Fragment, useEffect, useState } from 'react';

import orderBy from 'lodash/orderBy';
import PropTypes from 'prop-types';

import AutoResizer from './AutoResizer';
import BaseTable from './BaseTable';
import Column from './Column';
import GridHeader from './GridHeader';
import { Loader } from '../Loader';

import '@multiverses/verse-css/scss/components/BaseTable.scss';

export const Datagrid = ({
  tableId,
  data,
  height,
  schema,
  dynamicRowHeight,
  loading,
  title,
  loadData,
  sortBy,
  ...props
}) => {
  const [sort, setSort] = useState(sortBy);
  const [colData, setColData] = useState([]);
  const { columns, ...tableSchema } = schema;

  useEffect(() => {
    setColData(orderBy(data, [sort.key], [sort.order]));
  }, [data]);

  const renderOverlay = () => {
    if (loading) {
      return <Loader />;
    }

    return null;
  };

  const renderEmpty = () => {
    if (loading) {
      return null;
    }
    return <div className="empty">No data available</div>;
  };

  const onColumnSort = ({ key, order }) => {
    const newSort = {
      [key]: order,
    };
    setSort(newSort);
    setColData(orderBy(colData, [key], [order]));
  };

  const Footer = () => {
    return (
      <div className="footer">
        <div className="m-l-16 f-1 fb-400">
          Showing <span className="fb-500">{colData.length}</span> of{' '}
          {data.length || 0} results
        </div>
      </div>
    );
  };

  return (
    <div>
      <AutoResizer height={height}>
        {({ width, height }) => (
          <Fragment>
            <If condition={title}>
              <div className="header-wrapper" style={{ width }}>
                <GridHeader
                  colData={colData}
                  columns={columns}
                  data={data}
                  loadData={loadData}
                  setColData={setColData}
                  tableId={tableId}
                  title={title}
                />
              </div>
            </If>
            <BaseTable
              data={colData}
              disabled={loading}
              {...tableSchema}
              emptyRenderer={renderEmpty}
              estimatedRowHeight={dynamicRowHeight}
              footerHeight={0}
              footerRenderer={<Footer />}
              height={height}
              onColumnSort={onColumnSort}
              overlayRenderer={renderOverlay}
              sortBy={sort}
              width={width}
              {...props}
            >
              <For each="column" index="colIndex" of={columns}>
                <Column
                  {...column}
                  flexGrow={1}
                  key={column.dataKey}
                  resizable={true}
                />
              </For>
            </BaseTable>
          </Fragment>
        )}
      </AutoResizer>
    </div>
  );
};

Datagrid.propTypes = {
  data: PropTypes.array,
  dynamicRowHeight: PropTypes.number,
  height: PropTypes.number,
  loadData: PropTypes.func,
  loading: PropTypes.bool,
  schema: PropTypes.shape({
    columns: PropTypes.array,
  }),
  sortBy: PropTypes.shape({}),
  tableId: PropTypes.string,
  title: PropTypes.any,
};

Datagrid.defaultProps = {
  data: [],
  height: 400,
  loadData: null,
  loading: false,
  schema: {
    columns: [],
  },
  sortBy: {},
  tableId: 'grid',
  title: '',
};
