import { useEffect } from 'react';

import { useModel } from '@multiverses/verse-react/src/utility/hooks';
import { useForm } from '@multiverses/verse-react/src/utility/redux/slices/forms/formSlice';

import { Contents } from './Contents';
import { finityDB } from '../idb';

const defaultForm = {
  base: {},
  track: {},
};

function App() {
  const form = useForm();
  const finity = useModel('finity');

  useEffect(() => {
    async function initApp() {
      form.set({ model: 'finity', value: defaultForm });
    }
    initApp();
    finityDB.init();
  }, []);

  if (!finity.track) {
    return null;
  }

  return <Contents />;
}

export default App;
