import { Form } from '@multiverses/verse-react/src/components/Form';
import PropTypes from 'prop-types';

export const DisclosedQuantity = ({ model }) => {
  return (
    <Form.Input
      label="Disclosed Quantity"
      model={model}
      placeholder="Disclosed Quantity"
      type="number"
    />
  );
};

DisclosedQuantity.propTypes = {
  model: PropTypes.string,
};

DisclosedQuantity.defaultProps = {
  model: 'finity.base.price',
};
