/* eslint-disable import/order */
import { Route, Routes } from 'react-router-dom';

import { CardsPage } from './CardsPage';
import { contents } from '../apps';

export const Contents = () => {
  return (
    <div className="flex">
      <Routes>
        <Route
          element={
            <div className="p-12 flex-12">
              <h4>Welcome...</h4>
              <blockquote cite="Terry Pratchett">
                So much universe, and so little time.
              </blockquote>
              <article>
                <h4>Resources and Credits</h4>
                <p>TODO...</p>
              </article>
            </div>
          }
          path="/*"
        />
        <Route element={<CardsPage cards={contents.Apps} />} path="/apps/*" />
        <Route element={<CardsPage cards={contents.Tools} />} path="/tools/*" />
      </Routes>
    </div>
  );
};
