import { useEffect, useState } from 'react';

import { Navbar } from '@multiverses/verse-react/src/components/Navigation/Navbar';
import {
  useLocation,
  useNavigate,
} from '@multiverses/verse-react/src/components/Route';
import { SideBar } from '@multiverses/verse-react/src/components/SideBar';

import { mainTools } from './apps';
import { Contents } from './contents';
window.Buffer = window.Buffer || require("buffer/").Buffer;

function App() {
  const [showSideNav, setShowSideNav] = useState(true);
  const [title, setTitle] = useState('Multiverse');
  const navigate = useNavigate();
  const { pathname } = useLocation();

  const onMenuClick = () => {
    setShowSideNav(!showSideNav);
  };

  useEffect(() => {
    console.log(`pathname`, pathname);
    const pathnames = pathname.split('/');
    const title = pathnames[2] || pathnames[1];
    if (title) {
      setTitle(` - ${title}`);
    } else {
      setTitle('');
    }
  }, [pathname]);

  const goToHome = () => {
    navigate('/');
  };

  return (
    <div className="light-theme">
      <Navbar onMenuClick={onMenuClick} showMenu={showSideNav}>
        <Navbar.NavItem>
          <b className="navitem-title c-pointer" onClick={goToHome}>
            Multiverse
          </b>
          <span className="subtitle">{title}</span>
        </Navbar.NavItem>
      </Navbar>{' '}
      <div className="flex">
        <SideBar contents={mainTools} show={showSideNav} />
        <div className="flex-9">
          <Contents />
        </div>
      </div>
    </div>
  );
}

export default App;
