import { Route, Routes } from '@multiverses/verse-react/src/components/Route';
import { SideBar } from '@multiverses/verse-react/src/components/SideBar';

import { contents } from '../tools';
import { CSVToJSON } from './CSVToJSON';

export const Contents = () => {
  return (
    <div className="flex h-100VH">
      <div className="flex-10 primary">
        <SideBar contents={contents} isLink={true} />
      </div>
      <div className="flex-90">
        <Routes>
          <Route element={<CSVToJSON />} path="/" />
          <Route element={<CSVToJSON />} path="/csvToJSON" />
        </Routes>
      </div>
    </div>
  );
};
