import exponentialMovingAverage from '@multiverses/verse-utility/technicalIndicators/exponentialMovingAverage';

import { BackTestResults } from '../../../component/BackTestResults';
import { brokerage } from '../../../functions/brokerage';

export const ema = [
  {
    name: 'EMA Up Down',
    description:
      'Runs EMA combos and gives the highest probability EMA for profit.',
    calculation: ({
      baseSet,
      trackSet,
      product = 'options',
      valueKey = 'close',
      quantity,
    }) => {
      let buySet = trackSet || baseSet;
      const emas = [1, 2, 3, 5, 7, 9, 11, 14, 15, 17, 20];
      const emaValues = {};
      emas.forEach(ema => {
        const p1 = { datasets: baseSet, period: ema, valueKey };
        emaValues[ema] = exponentialMovingAverage(p1);
      });
      const emaCombos = [
        { e1: 1, e2: 3 },
        { e1: 1, e2: 5 },
        { e1: 2, e2: 3 },
        { e1: 3, e2: 7 },
        { e1: 7, e2: 14 },
        { e1: 9, e2: 20 },
      ];
      const finalResults = [];
      emaCombos.forEach(emaCombo => {
        const comboName = `${emaCombo.e1} Fast EMA -${emaCombo.e2} Slow EMA`;
        const slicelength = emaCombo.e2 - emaCombo.e1;
        const ema1s = emaValues[emaCombo.e1].slice(
          slicelength,
          emaValues[emaCombo.e1].length
        );
        const ema2s = emaValues[emaCombo.e2];
        const emaLength = ema1s.length;
        const decisions = [];

        let hasBuyOrder = false;
        let decision = { type: 'none' };
        let lastBuyPrice = 0;
        for (let i = 0; i < emaLength; i++) {
          /** Has No Buy Order, if fast ema beats slow ema, make a buy */
          const currentCandle = buySet[i + slicelength + 1];
          if (!hasBuyOrder && ema1s[i].ema > ema2s[i].ema && currentCandle) {
            hasBuyOrder = true;
            decision = {
              type: 'Buy',
              datetime: currentCandle.datetime,
              price: currentCandle.close,
            };
            lastBuyPrice = currentCandle.close;
          } else if (
            hasBuyOrder &&
            (ema2s[i].ema > ema1s[i].ema ||
              lastBuyPrice >= currentCandle?.close + 2)
          ) {
            decision = {
              type: 'Sell',
              datetime: currentCandle.datetime,
              price: currentCandle.close,
            };
            hasBuyOrder = false;
          }
          decisions.push(decision);
          decision = { type: 'none' };
        }
        if (hasBuyOrder) {
          let sellCandle = buySet[buySet.length - 1];
          decision = {
            type: 'Sell',
            datetime: sellCandle.datetime,
            price: sellCandle.close,
          };
          decisions.push(decision);
        }

        const {
          orders,
          winCount,
          loseCount,
          pnl,
          pnlBeforeExpense,
          charges,
          winPct,
          losePct,
          orderCount,
        } = brokerage(
          product,
          decisions.filter(
            decision => decision.type === 'Sell' || decision.type === 'Buy'
          ),
          quantity
        );

        finalResults.push({
          comboName,
          pnl,
          pnlBeforeExpense,
          charges,
          orders,
          winCount,
          loseCount,
          winPct,
          losePct,
          orderCount,
        });
      });

      return finalResults;
    },
    component: results => (
      <div className="r-flex-15">
        <BackTestResults result={results} />
      </div>
    ),
  },
];
