import { Progress } from '@multiverses/verse-react/src/components/Progress';
import { Skeleton } from '@multiverses/verse-react/src/components/Skeleton';
import { Text } from '@multiverses/verse-react/src/components/Text';
import PropTypes from 'prop-types';

const statusCode = {
  R: 0,
  Q: 25,
  O: 50,
  P: 75,
  E: 100,
  J: 100,
  X: 100,
  C: 100,
  undefined: 'Status message is unavailable.',
};

const statusMessage = {
  R: 'Requested, Request taken by ICICI direct system.',
  Q: 'Queued, Request sent to Exchange.',
  O: 'Request is open at Exchange.',
  P: 'Request is partly filled at Exchange.',
  E: 'Request is executed at Exchange.',
  J: 'Request is rejected by exchange/ICICI direct system.',
  X: 'Request expired by exchange/ICICI direct system',
  C: 'Cancelled.',
  undefined: 'Status message is unavailable.',
};

const orderClass = {
  Buy: 'info',
  Sell: 'error',
};

export const OrderStatus = ({ results }) => {
  const lastOrder = results[results.length - 1] || {};

  return (
    <div className={`live-result-card ${orderClass[lastOrder.type]}`}>
      <div className="m-x-4">
        <div className="flex space-btw">
          <div className="flex-25">
            <Skeleton keyName="Txn Type">{lastOrder.type}</Skeleton>
          </div>
          <div className="flex-25">
            <Skeleton keyName="Price">
              <Text>{lastOrder.price}</Text>
            </Skeleton>
          </div>
          <div className="flex-25">
            <Skeleton keyName="Quantity">{lastOrder.quantity}</Skeleton>
          </div>
          <div className="flex-25">
            <Skeleton keyName="Time">{lastOrder.datetime}</Skeleton>
          </div>
        </div>
        <div className="m-t-32">
          <Choose>
            <When
              condition={lastOrder.type === 'Buy' || lastOrder.type === 'Sell'}
            >
              <div className="fb-700 fb-1">
                {statusMessage[lastOrder.orderStatus]}
              </div>
              <Progress
                max={100}
                model="order.state"
                value={statusCode[lastOrder.orderStatus]}
              />
            </When>
            <Otherwise>
              <div className="empty">No orders to process.</div>
            </Otherwise>
          </Choose>
        </div>
      </div>
    </div>
  );
};

OrderStatus.propTypes = {
  results: PropTypes.arrayOf(
    PropTypes.shape({
      date: PropTypes.string,
      datetime: PropTypes.string,
      expense: PropTypes.string,
      orderReference: PropTypes.string,
      orderStatus: PropTypes.string,
      price: PropTypes.string,
      quantity: PropTypes.string,
      stockCode: PropTypes.string,
      type: PropTypes.string,
    })
  ),
};

OrderStatus.defaultProps = {
  results: [],
};
