import { Form } from '@multiverses/verse-react/src/components/Form';
import PropTypes from 'prop-types';

export const Stoploss = ({ model }) => {
  return (
    <Form.Input
      label="Stop Loss"
      model={model}
      placeholder="Stoploss"
      step="0.01"
      type="number"
    />
  );
};

Stoploss.propTypes = {
  model: PropTypes.string,
};

Stoploss.defaultProps = {
  model: 'finity.stoploss',
};
