import { useEffect, useState } from 'react';

import { Form } from '@multiverses/verse-react/src/components/Form';
import PropTypes from 'prop-types';

import { useModel } from '../../../../../../verse-react/src/utility/hooks';
import { useApp } from '../../../../AppProvider';

export const ExpiryDate = ({ model }) => {
  const finity = useModel('finity', {});
  const [options, setOptions] = useState([]);
  const { expiry = {} } = useApp();

  useEffect(() => {
    // console.log('AA', finity.trackStockCode);
    const products = expiry[finity.track.product] || {};
    const expiryDates = products?.[finity.track.stockCode] || {};
    const derivativeOptions = Object.keys(expiryDates) || [];
    // console.log('AA', derivativeOptions);
    setOptions(derivativeOptions);
  }, [finity.track.stockCode]);

  return (
    <Form.Select
      defaultValue={options[1]}
      label="Expiry Date"
      model={model}
      options={options}
      placeholder="Expiry Date"
    />
  );
};

ExpiryDate.propTypes = {
  model: PropTypes.string,
};

ExpiryDate.defaultProps = {
  model: 'finity.expiryDate',
};
