import React from 'react';

import { Datagrid } from '@multiverses/verse-react/src/components/Datagrid';
import PropTypes from 'prop-types';

import { brokerage } from '../../../functions/brokerage';

const schema = {
  columns: [
    {
      dataKey: 'orderReference',
      title: 'Order ID',
      width: 120,
      dataType: 'String',
    },
    {
      dataKey: 'datetime',
      title: 'Time',
      width: 100,
      dataType: 'String',
      sortable: true,
    },
    {
      dataKey: 'type',
      title: 'Type',
      width: 60,
      dataType: 'String',
    },
    {
      dataKey: 'stockCode',
      title: 'Stock Code',
      width: 100,
      dataType: 'String',
    },
    {
      dataKey: 'price',
      title: 'Price',
      width: 80,
      dataType: 'String',
    },
    {
      dataKey: 'quantity',
      title: 'Qty',
      width: 80,
      dataType: 'String',
    },
    {
      dataKey: 'pnl',
      title: 'PnL',
      width: 80,
      dataType: 'String',
      cellRenderer: ({ rowIndex, rowData, container }) => {
        if (rowData.type === 'Sell' && rowData.orderStatus === 'E') {
          if (container.props.data[rowIndex + 1]) {
            const result = brokerage('options', [
              container.props.data[rowIndex + 1],
              container.props.data[rowIndex],
            ]);
            return <React.Fragment>{result.pnl}</React.Fragment>;
          }
        } else {
          return '---';
        }
      },
    },
  ],
};

export const Orders = ({ results }) => {
  return (
    <div>
      <Datagrid
        data={results}
        height={318}
        rowKey="orderReference"
        schema={schema}
        sortBy={{ key: 'orderReference', order: 'desc' }}
      />
    </div>
  );
};

Orders.propTypes = {
  results: PropTypes.shape({
    buyCount: PropTypes.number,
    charges: PropTypes.number,
    comboName: PropTypes.string,
    loseCount: PropTypes.number,
    orderCount: PropTypes.number,
    orders: PropTypes.arrayOf(PropTypes.shape({})),
    pnl: PropTypes.number,
    pnlBeforeExpense: PropTypes.number,
    sellCount: PropTypes.number,
    winCount: PropTypes.number,
    winPct: PropTypes.number,
  }),
};

Orders.defaultProps = {
  orders: [],
};
