import { Expandable } from '@multiverses/verse-react/src/components/Expandable';

import { useModel } from '../../../../verse-react/src/utility/hooks';
import { BaseForm } from '../common/Forms/BaseForm';
import { OptionBaseForm } from '../common/Forms/OptionForm';
import { OrderForm } from '../common/Forms/OrderForm';

export const Execute = () => {
  const finity = useModel('finity');
  return (
    <div>
      <Expandable title={finity.base.stockCode}>
        <BaseForm />
        <OrderForm />
        <Choose>
          <When condition={['options'].includes(finity.base.product)}>
            <OptionBaseForm />
          </When>
          <When condition={['futures'].includes(finity.base.product)}>
            <OptionBaseForm />
          </When>
        </Choose>
      </Expandable>
    </div>
  );
};
