import mean from '@multiverses/verse-utility/array/mean';
import round from '@multiverses/verse-utility/utility/round';

/** Accumulation Swing Index
 
Enter Long (Buy) at the current daily price close of the Dow-Jones Industrial Average when the current ASI is greater than yesterday’s 2-day exponential moving average of ASI.

Close Long (Sell) at the current daily price close of the Dow-Jones Industrial Average when the current ASI is less than yesterday’s 2-day exponential moving average of ASI.

Enter Short (Sell Short) at the current daily price close of the Dow-Jones

Industrial Average when the current ASI is less than yesterday’s 2-day exponential moving average of ASI.
Close Short (Cover) at the current daily price close of the Dow-Jones Industrial Average when the current ASI is greater than yesterday’s 2-day exponential moving average of ASI.

*/

const ema = (dataset, n, prevExp, valueKey) => {
  return (dataset[valueKey] - prevExp) * (2 / (n + 1)) + prevExp;
};

const exponentialMovingAverage = params => {
  let { datasets, period = 9, valueKey = 'close' } = params;
  // EMA = (C - Ep)K + Ep
  const emaValues = [];
  const emaSets = datasets.slice(0, period);

  const smaOfDay = mean(emaSets, period, valueKey);

  for (let i = period; i < datasets.length; i++) {
    const prevExp = emaValues[emaValues.length - 1]?.ema || smaOfDay;
    emaValues.push({
      ema: ema(datasets[i], period, prevExp, valueKey),
      datetime: datasets[i].datetime,
      [valueKey]: datasets[i][valueKey],
      close: datasets[i].close,
      open: datasets[i].open,
    });
  }
  return [...emaValues];
};

export default exponentialMovingAverage;
