import { useMemo } from 'react';

import { Skeleton } from '@multiverses/verse-react/src/components/Skeleton';
import { Text } from '@multiverses/verse-react/src/components/Text';
import PropTypes from 'prop-types';

import { brokerage } from '../../../functions/brokerage';

export const Brokerage = ({ orders }) => {
  const results = useMemo(() => brokerage('options', orders), [orders.length]);

  return (
    <div className="header-wrapper">
      <div className="header m-l-12">
        <div className="r-flex-20 m-b-8">
          <div className="flex">
            <div className="r-flex-3">
              <Skeleton keyName="EBE" title="PNL before expenses">
                <Text format="currency_inr">{results.pnlBeforeExpense}</Text>
              </Skeleton>
            </div>
            <></>
            <div className="r-flex-3">
              <Skeleton keyName="Charges">
                <Text format="currency_inr">{results.charges}</Text>
              </Skeleton>
            </div>
            <div className="r-flex-3">
              <Skeleton keyName="Orders">{results.orderCount}</Skeleton>
            </div>
            <div className="r-flex-2">
              <Skeleton keyName="Winners">{results.winCount}</Skeleton>
            </div>
            <div className="r-flex-2">
              <Skeleton keyName="Losers">{results.loseCount}</Skeleton>
            </div>
            <div className="r-flex-3">
              <Skeleton keyName="Win %">{results.winPct}</Skeleton>
            </div>
            <div className="r-flex-3">
              <Skeleton keyName="PnL">
                <Text format="currency_inr">{results.pnl}</Text>
              </Skeleton>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

Brokerage.propTypes = {
  orders: PropTypes.arrayOf(PropTypes.shape({})),
};

Brokerage.defaultProps = {
  orders: [],
};
