import { parseLiveOrder } from './parseLiveOrder';
import { callApi } from '../../../hooks/useFinityApi';
import { initWS } from '../helpers';

export class OrderStream {
  constructor() {
    this.orderId;
  }

  init = async () => {
    if (this.promise) {
      return this.promise;
    }
    try {
      this.socket = await initWS('ORDERFEED');
      this.promise = this.socket;
    } catch (err) {
      console.log('Socket initialization error', err);
      return err.message;
    }
  };

  disconnect = () => {
    this.socket.disconnect();
  };

  watch = async token => {
    this.socket.emit('join', token);
  };

  on = async () => {
    this.socket.on('order', data => {
      const parsedData = parseLiveOrder(data);
      this.cb(parsedData);
    });
  };

  call = async (finity, action, cb) => {
    console.log(`finity`, action, finity);
    this.cb = cb;
    try {
      console.log(`finity`, action, finity, callApi);
      const orderResponse = await callApi('order', {
        data: finity,
        type: 'track',
        action,
      });
      console.log(`orderResponse`, orderResponse);
      if (orderResponse.error?.message) {
        return false;
      } else {
        const orderData = orderResponse.payload.order_id;
        return orderData;
      }
    } catch (err) {
      return false;
    }
  };

  demoCall = async (action, ltData, onOrder, resolve) => {
    const orderReference = (localStorage.get('orderReference') || 0) + 1;
    const status = ['R', 'Q', 'P', 'E'];
    for (let i = 0; i < status.length; i++) {
      const orderData = {
        stockCode: ltData.symbol,
        messageTime: ltData.datetime,
        messageDate: ltData.datetime,
        limitRate: ltData.close,
        averageExecutedRate: ltData.close,
        price: ltData.close,
        orderFlow: action,
        orderReference,
        orderStatus: status[i],
        orderTotalQuantity: ltData.quantity,
      };
      localStorage.set('orderReference', orderReference);
      this.onOrder(orderData);
    }
    resolve(true);
  };
}
