import { Form } from '@multiverses/verse-react/src/components/Form';
import PropTypes from 'prop-types';

const options = ['limit', 'market', 'stoploss'];

export const OrderType = ({ model }) => {
  return (
    <Form.Select
      defaultValue={options[0]}
      label="Order Type"
      model={model}
      options={options}
      placeholder="Order Type"
    />
  );
};

OrderType.propTypes = {
  model: PropTypes.string,
};

OrderType.defaultProps = {
  model: 'finity.orderType',
};
