// import { Clock } from '@multiverses/verse-react/src/components/Clock';
import { MaterialIcon } from '@multiverses/verse-react/src/components/Icon/MaterialIcon';
import { Skeleton } from '@multiverses/verse-react/src/components/Skeleton';
import { localStorage } from '@multiverses/verse-react/src/utility/storage';
import PropTypes from 'prop-types';

import { finityDB } from '../../../../idb';

export const LiveStatus = ({ bClose, tClose, onDisconnect }) => {
  const clearAll = () => {
    localStorage.clear();
    Object.keys(finityDB.stores).forEach(store => {
      finityDB[store].clear();
    });
    window.location.reload();
  };

  return (
    <div className="live-result-card-left">
      <div className="p-x-8 primary">
        <div>
          <Skeleton keyName="Base">{bClose}</Skeleton>
        </div>
        <div className="m-t-16">
          <Skeleton keyName="Track">{tClose}</Skeleton>
        </div>
        {/* <div className="flex-40">
            <Clock paused={true} />
          </div> */}
      </div>
      <div className="flex flex-center-end h-50">
        <div className="m-r-8">
          <MaterialIcon
            className="error-icon"
            name="stop"
            onClick={onDisconnect}
            title="Pause All"
          />
        </div>
        <div className="m-r-4">
          <MaterialIcon
            className="error-icon"
            name="delete"
            onClick={clearAll}
            title="Clear All"
          />
        </div>
      </div>
    </div>
  );
};

LiveStatus.propTypes = {
  onDisconnect:PropTypes.func.isRequired,
  bClose: PropTypes.number,
  tClose: PropTypes.number,
};

LiveStatus.defaultProps = {
  bClose: 0,
  tClose: 0,
};
