import { useEffect, useState } from 'react';

import { Form } from '@multiverses/verse-react/src/components/Form';
import { useModel } from '@multiverses/verse-react/src/utility/hooks';

import { csvToJSON } from '../../resources/functions/csvToJSON';

export const CSVToJSON = () => {
  const formData = useModel('csvToJSON');
  const [csvResults, setCSVResults] = useState({});

  const parseCSV = async fileData => {
    const csvResults = await csvToJSON(fileData, { path: 'fileData' });
    console.log('AAAA', csvResults);
    setCSVResults(csvResults);
  };

  useEffect(() => {
    if (formData.fileData) {
      parseCSV(formData.fileData);
    }
  }, [formData]);

  return (
    <div>
      <div className="flex m-l-4">
        <Form.Wrapper className="flex-20">
          <Form.Label>Upload CSV Folder</Form.Label>
          <Form.UploadFolder model="csvToJSON" />
        </Form.Wrapper>
      </div>
      <div className="flex">
        <For each="csvResult" of={Object.keys(csvResults)}>
          <div className="m-x-12">
            <a download={csvResult} href={csvResults[csvResult]}>
              {csvResult}
            </a>
          </div>
        </For>
      </div>
    </div>
  );
};
