import { Fragment, useEffect } from 'react';

import PropTypes from 'prop-types';

let alertTimer;

export const Alert = ({
  type,
  message,
  errorMessage,
  dismissButton,
  onDismiss,
  className,
  autoDismiss,
  dismissTimeOut,
}) => {
  const alertMessage = errorMessage || message;
  const alertType = errorMessage ? 'error' : type;
  
  useEffect(() => {
    if (autoDismiss && alertMessage) {
      alertTimer = () => setTimeout(() => onDismiss(), dismissTimeOut);
      console.log(dismissTimeOut)
      alertTimer();
    }
    return () => {
      if (autoDismiss) {
        clearTimeout(alertTimer);
      }
    };
  }, [alertMessage]);


  return (
    <Fragment>
      {alertType && alertMessage && (
        <div
          className={`alert ${alertType} alert-dismissible fade show ${className}`}
          role="alert"
        >
          <div className="flex-95">{alertMessage}</div>
          <div className="flex-5">
            {dismissButton ||
              (onDismiss && (
                <button
                  aria-label="Close"
                  className="close"
                  data-dismiss="alert"
                  onClick={onDismiss}
                  type="button"
                >
                  <span aria-hidden="true">&times;</span>
                </button>
              ))}
          </div>
        </div>
      )}
    </Fragment>
  );
};

Alert.propTypes = {
  autoDismiss: PropTypes.bool,
  className: PropTypes.string,
  dismissButton: PropTypes.bool,
  dismissTimeOut: PropTypes.number,
  errorMessage: PropTypes.string,
  message: PropTypes.node,
  onDismiss: PropTypes.func,
  type: PropTypes.oneOf([
    '',
    'primary',
    'secondary',
    'success',
    'danger',
    'warning',
    'info',
    'light',
    'dark',
  ]),
};

Alert.defaultProps = {
  autoDismiss: false,
  className: '',
  dismissButton: false,
  dismissTimeOut: 3250,
  errorMessage: '',
  message: undefined,
  onDismiss: null,
  type: 'success',
};
