import { Form } from '@multiverses/verse-react/src/components/Form';
import PropTypes from 'prop-types';

export const Price = ({ model }) => {
  return (
    <Form.Input
      label="Price"
      model={model}
      placeholder="Price"
      step="0.05"
      type="number"
    />
  );
};

Price.propTypes = {
  model: PropTypes.string,
};

Price.defaultProps = {
  model: 'finity.price',
};
