import PropTypes from 'prop-types';

const formatters = {
  currency_inr: text => {
    return new Intl.NumberFormat('en-IN', {
      style: 'currency',
      currency: 'INR',
      maximumSignificantDigits: 3,
    }).format(text);
  },
};
export const Text = ({ children, format, align, texture }) => {
  let text = children;

  if (format && text) {
    text = formatters[format](text);
  }
  return <div className={`text-${align} font-${texture}`}>{text}</div>;
};

Text.propTypes = {
  align: PropTypes.string,
  children: PropTypes.node,
  format: PropTypes.string,
  texture: PropTypes.string,
};

Text.defaultProps = {
  align: 'left',
  children: '',
  texture: '',
};
