import { idb } from './index';

export class IDB {
  constructor(dbName, stores) {
    this.dbName = dbName;
    this.stores = stores;
    this.version = 1;
  }

  init = async () => {
    if (this.promise) {
      return this.promise;
    }

    try {
      this.promise = idb.initIDB(this.dbName, this.stores);
      const idbResponse = await this.promise;
      const idbKeys = Object.keys(idbResponse);
      idbKeys.forEach(idbKey => {
        const idbData = idbResponse[idbKey];
        this[idbKey] = idbData;
      });
    } catch (err) {
      console.log('IDB initialization error', err);
      return err.message;
    }
  };
}
