import { useEffect } from 'react';

import { Button } from '@multiverses/verse-react/src/components/Button';
import { Expandable } from '@multiverses/verse-react/src/components/Expandable';
import { Form } from '@multiverses/verse-react/src/components/Form';
import { useModel } from '@multiverses/verse-react/src/utility/hooks';
import { useForm } from '@multiverses/verse-react/src/utility/redux/slices/forms/formSlice';
import PropTypes from 'prop-types';

const model = 'idb.storeData';

export const IDBForm = ({ storeData, columns, stopEdit, handleEdit }) => {
  const { set } = useForm();
  const value = useModel(model);

  useEffect(() => {
    set({ model, value: storeData });
  }, []);

  const handleSubmit = () => {
    handleEdit(value);
  };

  return (
    <div>
      <Expandable onClose={stopEdit} position="top-right" showContent>
        <For each="column" index="idx" of={columns}>
          <div className="m-x-8" key={idx}>
            <Form.Input
              label={column.name}
              model={`idb.storeData.${column.keyPath}`}
              placeholder={column.name}
            />
          </div>
        </For>
        <div className="flex">
          <div className="flex-90"></div>
          <div className="flex-10">
            <Button
              className="btn success"
              // isLoading={historyState.loading}
              onClick={handleSubmit}
            >
              Edit
            </Button>
          </div>
        </div>
      </Expandable>
    </div>
  );
};

IDBForm.propTypes = {
  handleEdit: PropTypes.func.isRequired,
  stopEdit: PropTypes.func.isRequired,
  columns: PropTypes.arrayOf(),
  storeData: PropTypes.shape({}),
};

IDBForm.defaultProps = {
  columns: [],
  store: {},
};
