import PropTypes from 'prop-types';

export const Tab = ({ children, span, id, isActive, setTabId }) => {
  return (
    <div className={`flex-${span}`} onClick={() => setTabId(id)}>
      <div className="tabs-title">
        <div
          className={`flex flex-center p-x-8 radio-flat ${
            isActive ? 'primary' : 'primary-outline'
          }`}
        >
          <div className="flex-100">{children}</div>
        </div>
      </div>
    </div>
  );
};

Tab.propTypes = {
  id: PropTypes.number.isRequired,
  setTabId: PropTypes.func.isRequired,
  children: PropTypes.node,
  isActive: PropTypes.bool,
  span: PropTypes.number,
};

Tab.defaultProps = {
  isActive: false,
  span: 30,
};
