import { getTokenWS } from '../../../src/functions/stock';

const intervalOptions = {
  '1SEC': '1second',
  '1MIN': '1minute',
  '5MIN': '5minute',
  '30MIN': '30minute',
  '1DAY': '1day',
};

export const parseOHLCV = data => {
  const dataArray = data.split(',');
  const exchangeCode = dataArray[0];
  let outputData = {};
  outputData['exchangeCode'] = exchangeCode;
  outputData['stockCode'] = dataArray[1];
  if (exchangeCode === 'NSE') {
    outputData['low'] = dataArray[2];
    outputData['high'] = dataArray[3];
    outputData['open'] = dataArray[4];
    outputData['close'] = Number(dataArray[5]);
    outputData['volume'] = dataArray[6];
    outputData['datetime'] = dataArray[7];
    outputData['interval'] = intervalOptions[dataArray[8]];
  } else if (exchangeCode === 'NFO') {
    outputData['expiryDate'] = dataArray[2];
    const isOption = isNaN(dataArray[4]);
    if (isOption) {
      outputData['strikePrice'] = Number(dataArray[3]);
      outputData['right'] = dataArray[4];
      outputData['low'] = Number(dataArray[5]);
      outputData['high'] = dataArray[6];
      outputData['open'] = dataArray[7];
      outputData['close'] = Number(dataArray[8]);
      outputData['volume'] = dataArray[9];
      outputData['oi'] = dataArray[10];
      outputData['datetime'] = dataArray[11];
      outputData['interval'] = intervalOptions[dataArray[12]];
      outputData['product'] = 'options';
    } else {
      outputData['low'] = dataArray[3];
      outputData['high'] = dataArray[4];
      outputData['open'] = dataArray[5];
      outputData['close'] = Number(dataArray[6]);
      outputData['volume'] = dataArray[7];
      outputData['oi'] = dataArray[8];
      outputData['datetime'] = dataArray[9];
      outputData['interval'] = intervalOptions[dataArray[10]];
      outputData['product'] = 'futures';
    }
  }
  outputData['tokenLabel'] = getTokenWS(outputData);
  return outputData;
};
