import React, { useEffect, useState } from 'react';

import { Button } from '@multiverses/verse-react/src/components/Button';
import { Datagrid } from '@multiverses/verse-react/src/components/Datagrid';
import PropTypes from 'prop-types';

import { IDBForm } from './IDBForm';

export const IDBTable = ({ store, db }) => {
  const [schema, setSchema] = useState({ columns: [] });
  const [data, setData] = useState([]);
  const [editId, setEditId] = useState();

  const onEdit = cellData => {
    setEditId(cellData.rowIndex);
  };

  const refreshData = async () => {
    const tableData = await db[store].getAll();
    setData(tableData);
  };

  const onDelete = async cellData => {
    console.log('DB', db);
    await db[store].delete(
      cellData.rowData[db.stores[store].primaryOptions.keyPath]
    );
    refreshData();
  };

  const onDeleteAll = async () => {
    await db[store].clear();
    refreshData();
  };
  const stopEdit = () => {
    setEditId();
  };

  const handleEdit = async value => {
    await db[store].edit(value);
    refreshData();
    stopEdit();
  };

  const actions = {
    title: 'Actions',
    cellRenderer: cellData => (
      <Button
        className="btn error"
        // isLoading={historyState.loading}
        onClick={() => onDelete(cellData)}
      >
        Delete
      </Button>
    ),
    dataKey: 'index',
    width: 100,
  };

  const onStoreChg = async () => {
    refreshData();
    const schema = {
      columns: db.stores[store].index.map(item => ({
        dataKey: item.keyPath,
        title: item.name,
        width: 100,
      })),
    };
    schema.columns.push(actions);
    setSchema(schema);
  };

  useEffect(() => {
    async function onStoreChange() {
      if (store) {
        await onStoreChg();
      }
    }
    onStoreChange();
  }, [store]);

  return (
    <div>
      <Datagrid
        data={data}
        height={380}
        rowEventHandlers={{ onDoubleClick: onEdit }}
        schema={schema}
        title={
          <React.Fragment>
            <div className="flex-85">{store}</div>
            <div className="flex-15">
              <Button
                className="btn error"
                // isLoading={historyState.loading}
                onClick={onDeleteAll}
              >
                Delete All Rows
              </Button>
            </div>
          </React.Fragment>
        }
      />
      <If condition={editId >= 0}>
        <IDBForm
          columns={db.stores[store].index}
          handleEdit={handleEdit}
          stopEdit={stopEdit}
          storeData={data[editId]}
        />
      </If>
    </div>
  );
};

IDBTable.propTypes = {
  db: PropTypes.shape({
    stores: PropTypes.shape({}),
  }).isRequired,
  store: PropTypes.string,
};

IDBTable.defaultProps = {
  db: {},
};
