import { useState } from 'react';

import { Button } from '@multiverses/verse-react/src/components/Button';
import { Loader } from '@multiverses/verse-react/src/components/Loader';
import { useModel } from '@multiverses/verse-react/src/utility/hooks';

import { useFinityApi } from '../../../hooks/useFinityApi';
import { strategiesList } from '../../resources/strategies/backtest';
import { StrategyList } from '../common/FormFields/StrategyList';
import { TrackForm } from '../common/Forms/TrackForm';

export const BackTest = () => {
  const { historyState, historyRequest } = useFinityApi('history');
  const delay = ms => new Promise(res => setTimeout(res, ms));

  const [activeStrategy, setActiveStrategy] = useState({});
  const [results, setResults] = useState({});
  const finity = useModel('finity');

  const onStrategyChange = async e => {
    let result;
    if (e.target.outerText === 'Base') {
      const baseResponse = await historyRequest({
        data: finity,
        type: 'base',
      });
      console.log('baseResponse', baseResponse);
      result = strategiesList[finity.strategy].calculation({
        baseSet: baseResponse.payload.data,
        product: finity.base.product,
        quantity: finity.base.quantity,
      });
    } else {
      const baseResponse = await historyRequest({
        data: finity,
        type: 'base',
      });
      await delay(3000);
      const trackResponse = await historyRequest({
        data: finity,
        type: 'track',
      });
      result = strategiesList[finity.strategy].calculation({
        baseSet: baseResponse.payload.data,
        trackSet: trackResponse.payload.data,
        product: finity.track.product,
        quantity: finity.track.quantity,
      });
    }
    setActiveStrategy(strategiesList[finity.strategy]);
    setResults(result);
  };

  return (
    <div className="flex space-btw">
      <div className="r-flex-5">
        <div className="flex">
          <div className="r-flex-16">
            <StrategyList />
          </div>
          <div className="r-flex-4">
            <div className="m-t-28">
              <Loader isLoading={historyState.loading} />
            </div>
          </div>
        </div>
        <div className="flex space-btw m-t-12">
          <div className="r-flex-9">
            <Button className="btn info" onClick={onStrategyChange}>
              Base
            </Button>
          </div>
          <div className="r-flex-9">
            <Button className="btn success" onClick={onStrategyChange}>
              Track
            </Button>
          </div>
        </div>
        <TrackForm />
      </div>
      <div className="r-flex-15">
        <div className="p-l-8">
          {activeStrategy.component && activeStrategy.component(results)}
        </div>
      </div>
    </div>
  );
};
