var camelCase = function camelCase(str) {
  return str
    .toLowerCase()
    .replace(/[^a-zA-Z0-9]+(.)/g, (m, chr) => chr.toUpperCase());
};

export const csvToJSON = async (data = [], options = {}) => {
  const finalList = {};
  data.forEach(async (element, index) => {
    let csvData = element;
    console.log('element', element);
    const fileName = element.filePath.replace('.txt', '.json') || index;
    if (options.path) {
      csvData = element[options.path];
    }
    const dataArray = csvData.split(/\r?\n/);
    const columns = dataArray[0].split(',');
    dataArray.shift();
    const tempList = [];
    for (let i = 0; i < dataArray.length; i++) {
      const rowObject = {};
      rowObject.sNo = i + 1;
      const sList = dataArray[i].split(',');
      for (let j = 0; j < columns.length; j++) {
        let columnName = camelCase(
          columns[j].replace(new RegExp('"', 'g'), '').trim()
        );
        rowObject[columnName] = sList[j].replace(new RegExp('"', 'g'), '');
      }
      tempList.push(rowObject);
    }
    var jsonString = JSON.stringify(tempList);
    var blobObj = new Blob([jsonString], { type: 'application/json' });
    const dataURL = window.URL.createObjectURL(blobObj);
    console.log(dataURL);
    finalList[fileName] = dataURL;
  });
  return finalList;
};
