import React from 'react';

import PropTypes from 'prop-types';

import { Brokerage } from './Brokerage';
import { LiveStatus } from './LiveStatus';
// import { Logs } from './Logs';
import { Orders } from './Orders';
import { OrderStatus } from './OrderStatus';

export const LiveResults = ({ results, bData, tData, onDisconnect }) => {
  return (
    <React.Fragment>
      <div className="flex">
        <div className="r-flex-6">
          <LiveStatus
            bClose={bData.close}
            onDisconnect={onDisconnect}
            tClose={tData.close}
          />
        </div>
        <div className="r-flex-14">
          <OrderStatus results={results} />
        </div>
      </div>
      <div className="flex">
        <div className="r-flex-20">
          <Brokerage orders={results} />
        </div>
      </div>
      <div className="flex">
        {/* <div className="flex-30">
          <Logs logs={logs} />
        </div> */}
        <div className="r-flex-20">
          <Orders results={results} />
        </div>
      </div>
    </React.Fragment>
  );
};

LiveResults.propTypes = {
  onDisconnect:PropTypes.func.isRequired,
  bData: PropTypes.shape({
    close: PropTypes.number,
  }),
  logs: PropTypes.arrayOf(PropTypes.shape({})),
  results: PropTypes.shape({}),
  tData: PropTypes.shape({ close: PropTypes.number }),
};

LiveResults.defaultProps = {
  bData: {},
  logs: [],
  results: {},
  tData: {},
};
