import { useEffect } from 'react';

import { Accordion } from '@multiverses/verse-react/src/components/Accordion';
import { Alert } from '@multiverses/verse-react/src/components/Alert';
import { Button } from '@multiverses/verse-react/src/components/Button';
import { Card } from '@multiverses/verse-react/src/components/Card';
import { Form } from '@multiverses/verse-react/src/components/Form';
import { Skeleton } from '@multiverses/verse-react/src/components/Skeleton';
import { useModel } from '@multiverses/verse-react/src/utility/hooks/useModel';

import { useFinityApi } from '../../../../hooks';

export const Funds = () => {
  const formData = useModel('bank');
  const { fundsState, fundsRequest } = useFinityApi('funds');
  const { setFundsState, setFundsRequest, resetSetFundsMessage } = useFinityApi(
    'setFunds',
    {
      successMessage: 'Set Funds successfully',
    }
  );
  const { funds = {} } = fundsState;

  useEffect(() => {
    fundsRequest();
  }, []);

  const allocateFunds = async () => {
    await setFundsRequest({
      data: formData,
    });
    await fundsRequest({ cache: false });
  };
  console.log(setFundsState, resetSetFundsMessage);

  return (
    <div>
      <Card>
        <Accordion model="bank-account-accordion">
          <Accordion.Wrapper accordionId="account-details">
            <Accordion.Header>
              <Skeleton keyName="Allocated FNO">{funds.allocated_fno}</Skeleton>
              <Skeleton keyName="Blocked Balance">
                {funds.block_by_trade_balance}
              </Skeleton>
              <Skeleton keyName="Unallocated Balance">
                {funds.unallocated_balance}
              </Skeleton>
            </Accordion.Header>
            <Accordion.Content>
              <div className="flex flex-wrap">
                <Skeleton keyName="Bank Account Number">
                  {funds.bank_account}
                </Skeleton>
                <Skeleton keyName="Total Balance">
                  {funds.total_bank_balance}
                </Skeleton>
                <Skeleton keyName="Unallocated Balance">
                  {funds.unallocated_balance}
                </Skeleton>
                <Skeleton keyName="Blocked Balance">
                  {funds.block_by_trade_balance}
                </Skeleton>
              </div>
              <div className="flex flex-wrap m-t-8">
                <Skeleton keyName="Allocated Equity">
                  {funds.allocated_equity}
                </Skeleton>
                <Skeleton keyName="Allocated FNO">
                  {funds.allocated_fno}
                </Skeleton>
                <Skeleton keyName="Blocked Equity">
                  {funds.block_by_trade_equity}
                </Skeleton>
                <Skeleton keyName="Blocked FNO">
                  {funds.block_by_trade_fno}
                </Skeleton>
              </div>
            </Accordion.Content>
          </Accordion.Wrapper>
        </Accordion>

        <div className="m-t-12">
          <Skeleton keyName="Allocate Funds" />
          <div className="flex space-btw m-t-8 m-l-8">
            <div className="flex-25">
              <Form.Label>Transaction Type</Form.Label>
              <Form.Select
                defaultValue="Credit"
                model="bank.transaction_type"
                options={['Debit', 'Credit']}
              />
            </div>
            <div className="flex-25">
              <Form.Label>Amount</Form.Label>
              <Form.Input model="bank.amount" />
            </div>
            <div className="flex-25">
              <Form.Label>Segment</Form.Label>
              <Form.Select
                defaultValue="FNO"
                model="bank.segment"
                options={['Equity', 'FNO']}
              />
            </div>
            <div className="flex-15">
              <div className="m-t-8">
                <Button
                  className="btn-flat primary m-x-16"
                  loading={setFundsState.loading}
                  onClick={allocateFunds}
                >
                  Allocate
                </Button>
              </div>
            </div>
          </div>
        </div>
        <Alert
          autoDismiss
          className="m-x-8"
          errorMessage={setFundsState.error.message}
          message={setFundsState.successMessage}
          onDismiss={resetSetFundsMessage}
        />
      </Card>
    </div>
  );
};
