import React from 'react';

import { MaterialIcon } from '../../Icon';

const defaultRenderer = (contents, wrapper) => {
  return (
    <div className="flex">
      <For each="content" of={contents}>
        <React.Fragment key={content.name}>
          {React.createElement(
            wrapper,
            {
              ...content,
              className: 'sidebar-content',
            },
            <React.Fragment>
              <div>
                <MaterialIcon name={content.icon} title={content.name} />
              </div>
              <div className={`m-t-8 t-center`}>{content.name}</div>
            </React.Fragment>
          )}
        </React.Fragment>
      </For>
    </div>
  );
};

export default defaultRenderer;
