import { Form } from '@multiverses/verse-react/src/components/Form';
import PropTypes from 'prop-types';

const options = ['EMA Up Down'];

export const StrategyList = ({ model, defaultValue, ...props }) => {
  return (
    <Form.Select
      defaultValue={defaultValue}
      label="Strategy List"
      model={model}
      options={options}
      placeholder="Strategy List"
      {...props}
    />
  );
};

StrategyList.propTypes = {
  defaultValue: PropTypes.string,
  model: PropTypes.string,
};

StrategyList.defaultProps = {
  defaultValue: 'EMA Up Down',
  model: 'finity.strategy',
};
