import { Form } from '@multiverses/verse-react/src/components/Form';
import PropTypes from 'prop-types';

import { useModel } from '../../../../../../verse-react/src/utility/hooks';
import { useApp } from '../../../../AppProvider';

export const StockCode = ({ model, type, ...props }) => {
  const finity = useModel('finity', {});
  const { scripts = {} } = useApp();
  const options = scripts[finity[type]?.exchCode];

  return (
    <Form.Select
      defaultValue="NIFTY"
      label="Stock Code"
      labelKey="stockCode"
      model={model}
      options={options}
      placeholder="Stock Code"
      valueKey="stockCode"
      {...props}
    />
  );
};

StockCode.propTypes = {
  model: PropTypes.string,
  type: PropTypes.string,
};

StockCode.defaultProps = {
  model: 'finity.stockCode',
  type: 'base',
};
