import { Form } from '@multiverses/verse-react/src/components/Form';
import PropTypes from 'prop-types';

export const UserRemark = ({ model }) => {
  return (
    <Form.Input label="User Remark" model={model} placeholder="User Remark" />
  );
};

UserRemark.propTypes = {
  model: PropTypes.string,
};

UserRemark.defaultProps = {
  model: 'finity.userremark',
};
