import React from 'react';

import { ExpiryDate, Right, StrikePrice } from '../../../FormFields';

export const OptionForm = () => {
  return (
    <React.Fragment>
      <div className="flex space-btw m-t-12">
        <div className="r-flex-9">
          <Right model="finity.track.right" />
        </div>
        <div className="r-flex-9">
          <StrikePrice model="finity.track.strikePrice" />
        </div>
      </div>
      <div className="flex space-btw m-t-12">
        <ExpiryDate model="finity.track.expiryDate" />
      </div>
    </React.Fragment>
  );
};
