import { Form } from '@multiverses/verse-react/src/components/Form';
import PropTypes from 'prop-types';

// const options_v1 = ['minute', '5minute', '30minute', 'day'];
const options_v2 = ['1second', '1minute', '5minute', '30minute', '1day'];

export const Interval = ({ model }) => {
  return (
    <Form.Select
      defaultValue={options_v2[2]}
      label="Interval"
      model={model}
      options={options_v2}
      placeholder="Interval"
    />
  );
};

Interval.propTypes = {
  model: PropTypes.string,
};

Interval.defaultProps = {
  model: 'finity.interval',
};
