import { Children, cloneElement, isValidElement, useEffect } from 'react';

import PropTypes from 'prop-types';

import { useModel } from '../../utility/hooks';
import { useForm } from '../../utility/redux/slices/forms/formSlice';

export const Tabs = ({ children, defaultTab, model }) => {
  const { set } = useForm();
  const tabId = useModel(model);

  const setTabId = tabIndex => {
    set({ model, value: tabIndex });
  };

  useEffect(() => {
    if (defaultTab) {
      set({ model, value: defaultTab });
    }
  }, []);
  const childrenWithProps = Children.map(children, (child, index) => {
    if (isValidElement(child)) {
      return cloneElement(child, {
        tabId,
        setTabId,
        isActive: tabId === index,
      });
    }
    return child;
  });

  return <div className="tabs">{childrenWithProps}</div>;
};

Tabs.propTypes = {
  model: PropTypes.string.isRequired,
  children: PropTypes.node,
  defaultTab: PropTypes.number,
};

Tabs.defaultProps = {
  children: null,
  defaultTab: 1,
  model: 'default-tab-model',
};

export default Tabs;
