import { sessionStorage } from '@multiverses/verse-react/src/utility/storage';
import axios from 'axios';

function getMonthFromString(mon) {
  var d = Date.parse(mon + '1, 2012');
  if (!isNaN(d)) {
    return `${new Date(d).getMonth() + 1}`.padStart(2, 0);
  }
  return -1;
}

export const exchangeCodes = ['BSE', 'NSE', 'NDX', 'MCX', 'NFO'];

export const productCodes = {
  cash: 'EQUITY',
  futures: 'FUT',
  options: 'OPT',
};

export const monthCodes = {
  1: 'Jan',
  2: 'Feb',
  3: 'Mar',
  4: 'Apr',
  5: 'May',
  6: 'Jun',
  7: 'Jul',
  8: 'Aug',
  9: 'Sep',
  10: 'Oct',
  11: 'Nov',
  12: 'Dec',
};

export const exchDepth = {
  NSE: `4.1!`,
  NFO: `4.1!`,
  BSE: `1.1!`,
  NDX: `13.1!`,
  MCX: `6.1!`,
};

const getExpDate = finDate => {
  if (!finDate) {
    return '';
  }
  return `${finDate.substring(8, 10)}-${
    monthCodes[Number(finDate.substring(5, 7))]
  }-${finDate.substring(0, 4)}`;
};

const rightCodes = {
  call: 'CE',
  put: 'PE',
};

export const getToken = finity => {
  // console.log('finity :: ', finity);
  let label;
  switch (finity.product) {
    case 'futures':
      label = `FUT-${finity.stockCode}-${getExpDate(finity.expiryDate)}-${
        finity.strikePrice
      }`;
      break;
    case 'options':
      label = `OPT-${finity.stockCode}-${getExpDate(finity.expiryDate)}-${
        finity.strikePrice
      }-${rightCodes[finity.right]}`;
      break;
    default:
      label = finity.stockCode;
      break;
  }
  return label;
};

export const getTokenWS = finity => {
  // console.log('finity :: ', finity);
  let label;
  switch (finity.product) {
    case 'futures':
      label = `FUT-${finity.stockCode}-${finity.expiryDate}-${finity.strikePrice}`;
      break;
    case 'options':
      label = `OPT-${finity.stockCode}-${finity.expiryDate}-${finity.strikePrice}-${finity.right}`;
      break;
    default:
      label = finity.stockCode;
      break;
  }
  return label;
};

export const getTokenTick = finity => {
  // console.log('finity :: ', finity);
  let label;
  switch (finity.product) {
    case 'futures':
      label = `FUT-${finity.symbol}-${finity.expiryDate}-${finity.strikePrice}`;
      break;
    case 'options':
      label = `OPT-${finity.symbol}-${finity.expiryDate}-${finity.strikePrice}-${finity.right}`;
      break;
    default:
      label = finity.symbol;
      break;
  }
  return label;
};

export const getStock = async () => {
  const download =
    sessionStorage.get('stock-scripts') ||
    (await axios
      .get(
        'https://traderweb.icicidirect.com/Content/File/txtFile/ScripFile/StockScriptNew.csv'
      )
      .then(function (resp) {
        sessionStorage.set('stock-scripts', resp);
        return resp;
      }));
  const my_list = download.data.replaceAll('\r', '').split('\n');
  const scripts = {
    BSE: [],
    NSE: [],
    NDX: [],
    MCX: [],
    NFO: [],
  };
  const cdKeys = {
    BSE: {},
    NSE: {},
    NDX: {},
    MCX: {},
    NFO: {},
  };
  const futures = {};
  const options = {};
  const futureExpiry = {};
  const optionExpiry = {};
  const quantity = {};
  for (let i = 1; i < my_list.length; i++) {
    const row = my_list[i].split(',');
    if (row[2]) {
      let product = row[4];
      const contractDetail = row[7];
      const cdArray = contractDetail.split('-');
      let derivativeData = {};

      const basicData = {
        stockCode: row[0],
        stockName: row[1],
        exchCode: row[2],
        stockLabel: row[3],
        product,
        token: `${exchDepth[row[2]]}${row[5]}`,
        lotSize: Number(row[6]),
        contractDetail,
        nseCode: row[8],
        tickSize: Number(row[9]),
        right: '',
      };

      quantity[basicData.stockCode] = basicData.lotSize;

      if (['OPT', 'FUT'].includes(cdArray[0])) {
        const month = getMonthFromString(cdArray[3]);
        const expiry = `${cdArray[4]}-${month}-${cdArray[2]}`;
        if (cdArray[5]) {
          options[row[0]] = options[row[0]] || [];
          optionExpiry[row[0]] = optionExpiry[row[0]] || {};
          product = 'options';
          derivativeData.expiryDate = cdArray[2];
          derivativeData.expiryMon = month;
          derivativeData.expiryYear = cdArray[4];
          derivativeData.expiry = expiry;
          derivativeData.strike = Number(cdArray[5]);
          derivativeData.right = cdArray[6];
          options[row[0]].push({ ...basicData, ...derivativeData });
          optionExpiry[row[0]][expiry] = '';
        } else {
          futures[row[0]] = futures[row[0]] || [];
          futureExpiry[row[0]] = futureExpiry[row[0]] || {};
          product = 'futures';
          derivativeData.expiryDate = cdArray[2];
          derivativeData.expiryMon = month;
          derivativeData.expiryYear = cdArray[4];
          derivativeData.expiry = expiry;
          futures[row[0]].push({ ...basicData, ...derivativeData });
          futureExpiry[row[0]][expiry] = '';
        }
      }
      const outputData = {
        ...basicData,
        ...derivativeData,
      };
      scripts[row[2]].push(outputData);
      cdKeys[row[2]][basicData.contractDetail] = outputData;
    }
  }
  // console.log('cdKeys', cdKeys);
  const output = {
    exchangeCodes,
    scripts,
    futures,
    options,
    expiry: { futures: futureExpiry, options: optionExpiry },
    quantity,
    cdKeys,
  };
  return output;
};
