import React from 'react';

// import { Expandable } from '@multiverses/verse-react/src/components/Expandable';
import { useModel } from '@multiverses/verse-react/src/utility/hooks';

import { ExchangeCode, Product, Quantity, StockCode } from '../../FormFields';
import { FuturesTrackForm } from '../FuturesForm';
import { OptionTrackForm } from '../OptionForm';

export const TrackForm = () => {
  const finity = useModel('finity');
  return (
    <React.Fragment>
      <div className="flex space-btw m-t-12">
        <div className="r-flex-9">
          <ExchangeCode defaultValue="NFO" model="finity.track.exchCode" />
        </div>
        <div className="r-flex-9">
          <Product defaultValue="options" model="finity.track.product" />
        </div>
      </div>
      <div className="flex space-btw m-t-12">
        <div className="r-flex-12">
          <StockCode model="finity.track.stockCode" type="track" />
        </div>
        <div className="r-flex-7">
          <Quantity model="finity.track.quantity" type="track" />
        </div>
      </div>
      <Choose>
        <When condition={['options'].includes(finity.track.product)}>
          <OptionTrackForm />
        </When>
        <When condition={['futures'].includes(finity.track.product)}>
          <FuturesTrackForm />
        </When>
      </Choose>
      {/* <Expandable position="top-left" title={`Additional Options`}>
        A
      </Expandable> */}
    </React.Fragment>
  );
};
