import { useEffect } from 'react';

import PropTypes from 'prop-types';

import { useModel } from '../../utility/hooks';
import { useForm } from '../../utility/redux/slices/forms/formSlice';

export const Progress = ({ model, value, max, handleProgress }) => {
  const cValue = useModel(model);
  const { set } = useForm();

  const handleChange = () => {
    const progressValue = handleProgress ? handleProgress(set) : value;
    set({ model, value: progressValue || 0 });
  };

  useEffect(() => {
    handleChange();
  }, [value]);

  return <progress className="progress" max={max} value={cValue} />;
};

Progress.propTypes = {
  model: PropTypes.string.isRequired,
  handleProgress: PropTypes.func,
  max: PropTypes.node,
  value: PropTypes.node,
};

Progress.defaultProps = {
  handleProgress: null,
  max: 0,
  value: 0,
};
