import PropTypes from 'prop-types';

import { MaterialIcon } from '../Icon/MaterialIcon';

export const PlayPause = ({ play }) => {
  return (
    <Choose>
      <When condition={play}>
        <MaterialIcon className="primary-icon play" name="play_arrow" />
      </When>
      <Otherwise>
        <MaterialIcon className="error-icon" name="pause" />
      </Otherwise>
    </Choose>
  );
};

PlayPause.propTypes = {
  play: PropTypes.bool,
};

PlayPause.defaultProps = {
  play: false,
};
