import { localStorage } from '@multiverses/verse-react/src/utility/storage';

import { parseOHLCV } from './parseOHLCV';
import { callApi } from '../../../hooks/useFinityApi';
// import { delay } from '../../../src/functions/delay';
import { initWS } from '../helpers';

const stopStatus = [
  'P', // Pending
  'BR', // Buy Requested
  'BQ', // Buy Queue
  'BO', // Buy Ordered
  'BP', // Buy Part Filled
  'SR', // Sell Requested
  'SQ', // Sell Queue
  'SO', // Sell Ordered
  'SP', // Sell Part Filled
];

const pendingBuy = 'BE';

export class OHLCVStream {
  constructor() {
    this.history = {};
    this.interval = '5minute';
    this.isConnected = false;
    this.disconnected = false;
    this.orderStatus = localStorage.get('orderStatus');
    this.continue = true;
  }

  init = async tokens => {
    if (this.promise) {
      return this.promise;
    }
    try {
      this.socket = await initWS('OHLCV');
      this.promise = this.socket;
      this.isConnected = true;
      this.tokens = tokens;
    } catch (err) {
      console.log('Socket initialization error', err);
      return err.message;
    }
  };
  disconnect = () => {
    this.socket.disconnect();
    this.disconnected = true;
  };

  initHistory = async (token, finity, type) => {
    const history = await callApi('history', { data: finity, type });
    if (!this.history[token]) {
      this.history[token] = {
        [finity.interval]: history.data || [],
      };
    }
    return history.data;
  };

  watch = async token => {
    this.socket.emit('join', token);
  };

  on = async () => {
    this.socket.on('1MIN', data => {
      const parsedData = parseOHLCV(data);
      const dataToken =
        this.tokens[parsedData.exchangeCode][parsedData.tokenLabel].token;
      this.history[dataToken]['5minute'].push(parsedData);
      const trackSet = this.history[this.trackToken][this.interval];
      const trackData = trackSet[trackSet.length - 1];
      this.orderStatus = localStorage.get('orderStatus');
      const baseSet = this.history[this.baseToken][this.interval];
      const hasPendingBuy = this.orderStatus === pendingBuy;
      if (dataToken === this.baseToken) {
        this.setBase(baseSet[baseSet.length - 1]);
      } else {
        this.setTrack(trackData);
        if (!stopStatus.includes(this.orderStatus) && this.continue) {
          this.continue = false;
          this.promise = new Promise(resolve => {
            if (hasPendingBuy) {
              const shouldSell = this.sellLookUp(baseSet);
              if (shouldSell) {
                this.demoOrder('S', resolve);
              } else {
                resolve(true);
              }
            } else {
              const shouldBuy = this.buyLookUp(baseSet);
              if (shouldBuy) {
                this.demoOrder('B', resolve);
              } else {
                resolve(true);
              }
            }
          });
          this.promise.then(() => {
            this.continue = true;
          });
        }
      }
    });
  };

  demoOrder = async (action, resolve) => {
    const orderReference = (localStorage.get('orderReference') || 0) + 1;
    const status = ['R', 'Q', 'P', 'E'];
    for (let i = 0; i < status.length; i++) {
      const orderData = {
        stockCode: this.stockCode,
        messageTime: this.ltTrack.datetime,
        messageDate: this.ltTrack.datetime,
        limitRate: this.ltTrack.close,
        averageExecutedRate: this.ltTrack.close,
        price: this.ltTrack.close,
        orderFlow: action,
        orderReference,
        orderStatus: status[i],
        orderTotalQuantity: this.quantity,
      };
      localStorage.set('orderReference', orderReference);
      this.orderData = orderData;
      this.orderStatus = `${action}${status[i]}`;
      this.onOrder(orderData);
    }
    resolve(true);
  };

  demo = async () => {
    console.log('-----Starting demo-----');
    for (let i = 4; i < 72; i++) {
      await this.promise;
      const baseSet = this.history[this.baseToken]['5minute'].slice(0, i);
      const trackSet = this.history[this.trackToken]['5minute'];
      this.ltBase = baseSet[baseSet.length - 1];
      const ltTrackIndex = trackSet.findIndex(
        data => data.datetime === this.ltBase.datetime
      );
      this.ltTrack = trackSet[ltTrackIndex];
      this.orderStatus = localStorage.get('orderStatus');
      const hasPendingBuy = this.orderStatus === pendingBuy;
      if (!stopStatus.includes(this.orderStatus)) {
        this.promise = new Promise(resolve => {
          if (hasPendingBuy) {
            const shouldSell = this.sellLookUp(baseSet);
            if (shouldSell) {
              this.demoOrder('S', resolve);
            } else {
              resolve();
            }
          } else {
            const shouldBuy = this.buyLookUp(baseSet);
            if (shouldBuy) {
              this.demoOrder('B', resolve);
            } else {
              resolve();
            }
          }
        });
      }
      this.setBase(this.ltBase);
      this.setTrack(this.ltTrack);
    }
    console.log('$$', this.orderStatus);
    if (this.orderStatus === pendingBuy) {
      this.demoOrder('S', () => null);
    }
    this.onStop();
    console.log('-----Demo complete-----');
  };

  precheck = async params => {
    console.log('-----Starting precheck-----');
    this.buyLookUp = params.strategy.buyLookUp;
    this.sellLookUp = params.strategy.sellLookUp;
    this.setBase = params.setBase;
    this.setTrack = params.setTrack;
    this.baseToken = params.baseToken;
    this.trackToken = params.trackToken;
    this.onOrder = params.onOrder;
    this.onStop = params.onStop;
    this.stockCode = params.finity.track.stockCode;
    this.quantity = params.finity.track.quantity;
    this.interval = params.finity.track.interval;
    const baseHistory = await this.initHistory(
      this.baseToken,
      params.finity,
      'base'
    );
    console.log(`Base History------------${baseHistory.length}`);
    this.setBase(baseHistory[baseHistory.length - 1]);

    const trackHistory = await this.initHistory(
      this.trackToken,
      params.finity,
      'track'
    );
    console.log(`Track History------------${trackHistory.length}`);
    this.setTrack(trackHistory[trackHistory.length - 1]);

    await this.watch(this.baseToken);
    console.log(`Watching Base------------${this.baseToken}`);

    await this.watch(this.trackToken);
    console.log(`Watching Track------------${this.trackToken}`);
    console.log('-----Precheck complete-----');
  };
}
