import PropTypes from 'prop-types';

export const Skeleton = ({ keyName, title, children }) => {
  return (
    <>
      <div className="r-flex-12 m-t-4 m-b-4" title={title || keyName}>
        <span className="skeleton-label">{keyName}</span>
      </div>
      <div className="r-flex-12 skeleton-value">
        {children || <span>&mdash;</span>}
      </div>
    </>
  );
};

Skeleton.propTypes = {
  keyName: PropTypes.any.isRequired,
  children: PropTypes.any,
  title: PropTypes.string,
};

Skeleton.defultProps = {
  children: '',
};
