import { Button } from '@multiverses/verse-react/src/components/Button';
import { Form } from '@multiverses/verse-react/src/components/Form';
import { useModel } from '@multiverses/verse-react/src/utility/hooks';
import { localStorage } from '@multiverses/verse-react/src/utility/storage';
import PropTypes from 'prop-types';

import { IDBTable } from './IDBTable';

export const IDBManager = ({ db }) => {
  const store = useModel('idb.store');
  const stores = Object.keys(db.stores);

  const onDelete = () => {
    localStorage.clear();
    Object.keys(db.stores).forEach(store => {
      db[store].clear();
    });
  };
  return (
    <div className="flex">
      <div className="flex-20 p-r-8">
        <Form.Select
          defaultValue={stores[0]}
          label="Stores"
          model="idb.store"
          options={stores}
          placeholder="Stores"
        />
        <div className="flex space-btw m-t-8">
          <div className="flex-45">
            <Button className="btn error" onClick={onDelete}>
              Delete
            </Button>
          </div>
          <div className="flex-45">
            <Button className="btn success">Refresh</Button>
          </div>
        </div>
      </div>
      <div className="flex-80">
        <IDBTable db={db} store={store} />
      </div>
    </div>
  );
};

IDBManager.propTypes = {
  db: PropTypes.shape({
    stores: PropTypes.shape({}),
  }).isRequired,
};

IDBManager.defaultProps = {
  db: {},
};
