import { useEffect } from 'react';

import { Box } from '@multiverses/verse-react/src/components/Box';
import { Skeleton } from '@multiverses/verse-react/src/components/Skeleton';
import { useModel } from '@multiverses/verse-react/src/utility/hooks';
import { data } from 'browserslist';

import { useFinityApi } from '../../../../hooks/useFinityApi';
import { useApp } from '../../../AppProvider';

export const Dashboard = () => {
  const breeze = useApp();
  const finity = useModel('finity');
  const { gnewsState, gnewsRequest } = useFinityApi('gnews');
  const { historyState, historyRequest } = useFinityApi('history');
  const { gnews = {} } = gnewsState;
  const { history = {} } = historyState;

  useEffect(() => {
    if (finity.base.stockCode) {
      gnewsRequest({ data: { topic: finity.base.stockCode } });
      historyRequest({ data: finity, type: 'base' });
    }
  }, [finity.base.stockCode]);

  console.log(`breeze`, breeze);
  return (
    <div>
      <div className="flex space-btw">
        <div className="r-flex-9">
          <div className="flex-12">
            <div className="flex m-b-8">
              <Skeleton keyName="Latest Price">
                <b>{history.lastPrice}</b>
              </Skeleton>
            </div>
            <Skeleton keyName="Standard Deviation - 5">
              <div className="flex space-btw">
                <div className="r-flex-9">
                  <Box className="btn error">{history.sd?.lowerRange}</Box>
                </div>
                <div className="r-flex-9">
                  <Box className="btn success">{history.sd?.upperRange}</Box>
                </div>
              </div>
            </Skeleton>
            <Skeleton keyName="Standard Deviation - 30">
              <div className="flex space-btw">
                <div className="r-flex-9">
                  <Box className="btn error">{history.sd30?.lowerRange}</Box>
                </div>
                <div className="r-flex-9">
                  <Box className="btn success">{history.sd30?.upperRange}</Box>
                </div>
              </div>
            </Skeleton>
          </div>
        </div>
        <div className="r-flex-9">
          <div className="flex m-b-8">
            <Skeleton keyName="Date">
              <b>{gnews.currDate}</b>
            </Skeleton>
          </div>
          <Skeleton keyName="News Analysis">
            <div className="flex space-btw">
              <div className="r-flex-9">
                <Box className="btn error">{gnews.sellCount}</Box>
              </div>
              <div className="r-flex-9">
                <Box className="btn success">{gnews.buyCount}</Box>
              </div>
            </div>
          </Skeleton>
          <Skeleton keyName="Sentiment Analysis ( -3 to 3 )">
            <Choose>
              <When condition={gnews.analysis > 0}>
                <Box className="btn success">{gnews.analysis}</Box>
              </When>
              <When condition={gnews.analysis < 0}>
                <Box className="btn error">{gnews.analysis}</Box>
              </When>
              <Otherwise>
                <Box className="btn">{gnews.analysis}</Box>
              </Otherwise>
            </Choose>
          </Skeleton>
        </div>
      </div>
      <div className="flex space-btw">
        <div className="r-flex-9">
          <div className="flex space-btw">
            <For each="data" of={[...Array(10).keys()]}>
              <div className="r-flex-9">
                <Skeleton keyName={data + 1}>
                  <div
                    className={
                      history.data?.[history.data.length - (data + 1)]?.close -
                        history.data?.[history.data.length - (data + 2)]
                          ?.close >
                      0
                        ? 'success'
                        : 'error'
                    }
                  >
                    {history.data?.[history.data.length - (data + 1)]?.close -
                      history.data?.[history.data.length - (data + 2)]?.close}
                  </div>
                </Skeleton>
              </div>
            </For>
          </div>
        </div>
      </div>
    </div>
  );
};
