import { Form } from '@multiverses/verse-react/src/components/Form';
import PropTypes from 'prop-types';

const options = ['Track', 'Buy'];

export const BaseTracking = ({ model }) => {
  return (
    <Form.Select
      defaultValue="Track"
      label="Buy/Track"
      model={model}
      options={options}
    />
  );
};

BaseTracking.propTypes = {
  model: PropTypes.string,
};

BaseTracking.defaultProps = {
  model: 'finity.baseTracking',
};
