import { connect } from 'socket.io-client';

export function getCookie(name) {
  const value = `; ${document.cookie}`;
  const parts = value.split(`; ${name}=`);
  if (parts.length === 2) {
    return parts.pop().split(';').shift();
  }
  return '';
}

const sockets = {
  OHLCV: {
    url: 'https://breezeapi.icicidirect.com/',
    options: { path: '/ohlcvstream/' },
  },
  ORDERFEED: {
    url: 'https://livefeeds.icicidirect.com/',
  },
  TICKS: {
    url: 'https://livestream.icicidirect.com/',
  },
};

// Base 64 session_token from customer details API response ( Set as cookie in my API code for reusability )
export const getSessionToken = () => {
  return getCookie('session_token').replace('%3D', '=');
};

export const initWS = (name, options = {}) => {
  try {
    const sessionToken = getSessionToken();
    // Parse token to ascii string
    const parsedToken = Buffer.from(sessionToken, 'base64').toString('ascii');
    // Parsed session_token is split into two parts with colon as separator
    const splitToken = parsedToken.split(':');
    const url = sockets[name].url;
    const socketOptions = sockets[name].options || {};
    return connect(url, {
      ...socketOptions,
      auth: {
        user: splitToken[0], // Corresponds to 1st part of session_token which is same as idirect_userid from customer details API which contains 2 letters and 6 digits in it.
        token: splitToken[1], // Corresponds to 2nd part of session_token which is number of 9 digits.
      },
      timeout: 45000,
      extraHeaders: {
        'User-Agent': 'node-socketio[client]/socket',
      },
      transports: ['websocket'],
      ...options,
    });
  } catch (err) {
    console.log('Socket initialization error', err);
    return err.message;
  }
};
